import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import Editor from './editor';

import {selectUser, updateUsersData} from '../actions/actions';

class UserEditor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // edit: this.props.edit || false
      selected_user: this.props.all_users_data[this.props.selected_user] || {
        id: Math.random()
      }
    };
  }
  componentDidUpdate(prevProps) {
    if (this.props.selected_user) {
      if (this.props.selected_user !== prevProps.selected_user) {
        this.setState({
          selected_user: this.props.all_users_data[this.props.selected_user] || {
            id: Math.random()
          }
        });
      }
    }
  }

  handleCloseButton = (e) => {
    this.props.selectUser(null);
    setTimeout(()=>{
      this.setState({
        selected_user: {
          id: Math.random()
        }
      });
    },500);
  }

  handleChange = (e) => {
    this.setState({
      selected_user: {...this.state.selected_user, [e.target.name]:e.target.value}
    });
  }

  handleDescriptionChange = (value) => {
    this.setState({
      task_data: {...this.state.task_data, description:value}
    });
  }

  handleSaveChanges = (e) => {
    if (this.props.selected_user === "NEWUSER") {
      let id=Math.random();
      let new_user = {
        ...this.state.selected_user,
        trade_name: "",
        id: id,
        jobs: [],
        tasks: [],
        new_date: new Date(),
        status: 'new'
      }
      this.props.updateUsersData({
        [id]: new_user
      });
    } else {
      this.props.updateUsersData({
        [this.props.selected_user]: this.state.selected_user
      });
    }

    setTimeout(()=>{
      this.setState({
        selected_user: {
          id: Math.random()
        }
      });
    },500);
  }

  onRoleChange = (id) => {
    return (e) => {
      let roles = [...(this.state.selected_user.roles || [])];
      if (e.target.checked) {
        roles.push(id);
      } else {
        roles.splice(roles.indexOf(id),1);
      }
      this.setState({
        selected_user: {...this.state.selected_user, roles:roles}
      })
    }
  }

  render() {
    let role_nodes = [];

    for (var r in this.props.roles) {
      var role = this.props.roles[r];

      role_nodes.push((
        <label className="checkbox-container" key={role.id}>
          <input type="checkbox" onChange={this.onRoleChange(role.id)} checked={this.state.selected_user.roles && (this.state.selected_user.roles.indexOf(role.id)+1)} />
          <i className="fas fa-check-square"></i>
          <i className="far fa-square"></i>
          <span>{role.name}</span>
        </label>
      ));
    }


    return (
      <div className={"user-editor form" + (this.props.selected_user && this.props.all_users_data?" show":"")}>
        <div className="content">
          <header>
            <h1><i className="fas fa-phone"></i><span>{(this.state.selected_user.first_name || "") + " " + (this.state.selected_user.last_name || "")}</span></h1>
            <div>
              <button
                onClick={()=>{
                  this.handleSaveChanges();
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-check"></i>
                <span>save</span>
              </button>
              <button
                onClick={()=>{
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-times"></i>
                <span>cancel</span>
              </button>
            </div>
          </header>
          <main>
            <div>
              <div>
                <label>first name</label>
                <input name="first_name" type="text" placeholder="first name" value={this.state.selected_user.first_name || ""} onChange={this.handleChange} />
              </div>
              <div>
                <label>last name</label>
                <input name="last_name" type="text" placeholder="last name" value={this.state.selected_user.last_name || ""} onChange={this.handleChange} />
              </div>
              <div>
                <label>email</label>
                <input name="email" type="email" placeholder="email" value={this.state.selected_user.email || ""} onChange={this.handleChange} />
              </div>
              <div>
                <label>phone</label>
                <input name="phone" type="text" placeholder="phone" value={this.state.selected_user.phone || ""} onChange={this.handleChange} />
              </div>
            </div>
            <div>
              <div>
                <label>company name</label>
                <input name="company_name" type="text" placeholder="company name" value={this.state.selected_user.company_name || ""} onChange={this.handleChange} />
              </div>
              <div>
                <label>website</label>
                <input name="website" type="text" placeholder="website" value={this.state.selected_user.website || ""} onChange={this.handleChange} />
              </div>
              <div>
                <label>birthday</label>
                <input name="birthday" type="text" placeholder="birthday" value={this.state.selected_user.birthday || ""} onChange={this.handleChange} />
              </div>
            </div>
            <div>
              <div>
                <label>address</label>
                <textarea name="address" placeholder="address" value={this.state.selected_user.address || ""} onChange={this.handleChange} />
              </div>
            </div>
            <div className="roles-list">
              {role_nodes}
            </div>

            <div>
              <div>
                <label>notes</label>
                <Editor
                  defaultValue={this.state.selected_user.description || ""}
                  onChange={this.handleDescriptionChange}
                  refresh={this.props.selected_user || this.state.selected_user.id}
                />
              </div>
            </div>

          </main>
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    selected_user: state.state.selected_user,
    all_users_data: state.state.all_users_data,
    roles: state.state.roles
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({selectUser, updateUsersData}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(UserEditor);
