import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import Editor from './editor';

import DatePicker from 'react-datepicker';

import {selectTask, updateTasksData, addTask} from '../actions/actions';

const icons = [
  "fas fa-toolbox",
  "fas fa-paint-roller",
  "fas fa-wrench",
  "fas fa-screwdriver",
  "fas fa-hammer",
  "fas fa-tint",
  "fas fa-bolt",
  "fas fa-clipboard-list",
  "fas fa-tape",
  "fas fa-file-signature",
  "fas fa-dollar-sign"
];

class TaskEditor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // edit: this.props.edit || false
      icon: icons[0],
      task_data: this.props.all_tasks_data[this.props.selected_task] || {
        id: Math.random()
      }
    };
  }
  componentDidUpdate(prevProps) {
    if (this.props.selected_task) {
      if (this.props.selected_task !== prevProps.selected_task) {
        this.setState({
          task_data: this.props.all_tasks_data[this.props.selected_task] || {
            id: Math.random()
          }
        });
      }
    }
  }

  handleCloseButton = (e) => {
    this.props.selectTask(null);
  }

  handleChange = (e) => {
    this.setState({
      task_data: {...this.state.task_data, [e.target.name]:e.target.value}
    });
  }

  handleDescriptionChange = (value) => {
    this.setState({
      task_data: {...this.state.task_data, description:value}
    });
  }

  handleScheduleCheckChange = (changeEvent) => {
    console.log ()
    if (changeEvent.target.checked) {
      this.setState({
        enable_schedule: true,
        task_data: {...this.state.task_data, start_date: this.props.all_jobs_data[this.state.task_data.job_id].start_dates[this.props.selected_job_index], end_date: this.props.all_jobs_data[this.state.task_data.job_id].end_dates[this.props.selected_job_index]}
      });

    }
    else {
      this.setState({
        enable_schedule: false,
        task_data: {...this.state.task_data, start_date: null, end_date: null}
      });
    }
  };

  handleIconChange = (changeEvent) => {
    this.setState({
      icon: changeEvent.target.value
    });
    setTimeout(()=>{
      this.setState({
        icon: icons[0],
        task_data: {}
      });
    },500);
  };

  handleSaveChanges = (e) => {
    if (this.props.selected_task === "NEWTASK") {
      let id=Math.random();
      let new_task = {
        ...this.state.task_data,
        id: id,
        icon: this.state.icon,
        completed: false
      }
      this.props.addTask(id, new_task, this.props.selected_job, null);
    } else {
      this.props.updateTasksData({
        [this.props.selected_task]: this.state.task_data
      });
    }
    setTimeout(()=>{
      this.setState({
        icon: icons[0],
        task_data: {}
      });
    },500);
  }


  handleChangeStartTime = (value) => {
    this.setState({
      task_data: {...this.state.task_data, start_date: value}
    });
  }

  handleChangeEndTime = (value) => {
    this.setState({
      task_data: {...this.state.task_data, end_date: value}
    });
  }
  handleChangeDate = date => {
    let start_date = new Date(this.state.task_data.start_date.setFullYear(date.getFullYear(), date.getMonth(), date.getDate()));
    let end_date = new Date(this.state.task_data.end_date.setFullYear(date.getFullYear(), date.getMonth(), date.getDate()));
    this.setState({
      task_data: {...this.state.task_data, start_date: start_date, end_date: end_date}
    });
  };

  render() {
    let icon_radios = [];

    for (let i=0; i<icons.length; i++) {
      icon_radios.push ((
        <div key={"icon " + i} className="radio">
          <label>
            <input
              type="radio"
              value={icons[i]}
              checked={this.state.icon === icons[i]}
              onChange={this.handleIconChange}
            />
            <i className={icons[i]}></i>
          </label>
        </div>
      ));
    }

    return (
      <div className={"task-editor form" + (this.props.selected_task && this.props.all_tasks_data?" show":"")}>
        <div className="content">
          <header>
            <h1>
              {!this.props.selected_job && (<i className="fas fa-phone"></i>)}
              <span>{(this.state.task_data.first_name || "") + " " + (this.state.task_data.last_name || "")}</span>
            </h1>
            <div>
              <button
                onClick={()=>{
                  this.handleSaveChanges();
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-check"></i>
                <span>save</span>
              </button>
              <button
                onClick={()=>{
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-times"></i>
                <span>cancel</span>
              </button>
            </div>
          </header>
          <main>
            <div>
              <div>
                <label>name</label>
                <input name="name" type="text" placeholder="task name" value={this.state.task_data.name || ""} onChange={this.handleChange} />
              </div>
            </div>
            {!this.props.selected_job && (
              <div>
                <div>
                  <label>icon</label>
                  <div className="icon-picker">
                    {icon_radios}
                  </div>
                </div>
              </div>
            )}
            {!this.props.selected_job && (
              <div>
                <div>
                  <label>address</label>
                  <textarea name="address" placeholder="address" value={this.state.task_data.address || ""} onChange={this.handleChange} />
                </div>
              </div>
            )}

            <div>
              <label>
                <input
                  type="checkbox"
                  checked={(!!this.state.task_data.start_date)}
                  onChange={this.handleScheduleCheckChange}
                />
                <span>Schedule separately from parent job</span>
              </label>
            </div>
            {(this.state.task_data.start_date && this.state.task_data.end_date) && (
              <div>
                <div>
                  <label>on</label>
                  <DatePicker
                    selected={this.state.task_data.start_date}
                    onChange={this.handleChangeDate}
                    inline
                  />
                </div>
                <div>
                  <label>from</label>
                  <DatePicker
                    selected={new Date(this.state.task_data.start_date)}
                    className=""
                    inline
                    onChange={this.handleChangeStartTime}
                    showTimeSelect
                    showTimeSelectOnly
                    timeIntervals={15}
                    minTime={new Date(new Date(this.state.task_data.start_date).setHours(0, 0))}
                    maxTime={new Date(this.state.task_data.end_date)}
                    dateFormat="h:mm aa"
                    timeCaption="start time"
                  />
                </div>
                <div>
                  <label>to</label>
                  <DatePicker
                    selected={new Date(this.state.task_data.end_date)}
                    className=""
                    inline
                    onChange={this.handleChangeEndTime}
                    showTimeSelect
                    showTimeSelectOnly
                    timeIntervals={15}
                    minTime={new Date(this.state.task_data.start_date)}
                    maxTime={new Date(new Date(this.state.task_data.end_date).setHours(23, 55))}
                    dateFormat="h:mm aa"
                    timeCaption="end time"
                  />
                </div>
              </div>
            )}

            <div>
              <div>
                <label>notes</label>
                <Editor
                  defaultValue={(this.props.all_tasks_data[this.props.selected_task] && this.props.all_tasks_data[this.props.selected_task].description) || ""}
                  onChange={this.handleDescriptionChange}
                  refresh={this.props.selected_task || this.state.task_data.id}
                />
              </div>
            </div>
          </main>
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    selected_job: state.state.selected_job,
    selected_job_index: state.state.selected_job_index,
    selected_task: state.state.selected_task,
    all_jobs_data: state.state.all_jobs_data,
    all_tasks_data: state.state.all_tasks_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({selectTask, updateTasksData, addTask}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(TaskEditor);
