import React from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { updateLeadsData, selectLead, openProjectEditor, updateProjectsData, updateUsersData, selectJob } from '../actions/actions';

import {Droppable, Draggable } from 'react-beautiful-dnd';


const heat_map = [
  "white",
  "#fff3e9",
  "#ffe5cf",
  "#ffd7b6",
  "#ffc99c",
  "#ffbb83",
  "#ffad69",
  "#ff9f50",
  "#ff9136",
];

const max_heat = {
  new: 3,
  scheduled: 7,
  pending: 10,
  estimate: 10,
  approved: 10,
  rejected: 10,
};

const getItemStyle = (item, isDragging, draggableStyle) => {
  const heat = Math.min(Math.round(((new Date() - item[item.status + "_date"])/1000/60/60/24)*8/max_heat[item.status]),heat_map.length-1);

  return {
    // some basic styles to make the items look a bit nicer
    color: heat >= 8 ? "white" : "black",
    // change background colour if dragging
    background: heat_map[heat],//isDragging ? 'lightgreen' : 'grey',
    // boxShadow: "rgba(0, 0, 0, 0.12) 0 0 1rem",
    // styles we need to apply on draggables
    ...draggableStyle
  }
};

const getListStyle = isDraggingOver => ({
    background: isDraggingOver ? '#e4e4e4' : 'white',
    padding: ".5rem",
    width: "100%",
    flex: "1 1 auto"
});



class LeadKanban extends React.Component {
  state = {
    sort: "heat"
  }


  selectSort = (e) => {
    // let id=parseInt(e.currentTarget.dataset.id,10);
    console.log(e.target.value);
    this.setState({sort: e.target.value});
  }

  handleEstimateClick = (id) => {
    return (e) => {
      this.props.selectLead(id);
    };
  }

  handleLeadClick = (id) => {
    return (e) => {
      this.props.selectLead(id);
    };
  }

  createLead = (item, index) => {
    return (<Draggable
      key={item.id}
      draggableId={item.id}
      index={index}>
      {(provided, snapshot) => (
        <div
          ref={provided.innerRef}
          {...provided.draggableProps}
          {...provided.dragHandleProps}

          onClick={this.handleEstimateClick(item.id)}
          style={getItemStyle(
            item,
            snapshot.isDragging,
            provided.draggableProps.style
          )}>
          <div>{item.first_name + " " + item.last_name}</div>
        </div>
      )}
    </Draggable>);
  }

  // <div>
  //   <i
  //     className="fas fa-pen"
  //     onClick={this.handleLeadClick(item.id)}
  //   ></i>
  //   <i
  //     className="fas fa-file-invoice-dollar"
  //     onClick={this.handleEstimateClick(item.id)}
  //   ></i>
  // </div>


  render() {
    let leads = {
      new_leads: [],
      pending_leads: [],
      scheduled_leads: [],
      estimate_leads: [],
      approved_leads: [],
      rejected_leads: [],
      other_leads: []
    }
    for (let l in this.props.all_leads_data) {
      switch (this.props.all_leads_data[l].status) {
        case 'new':
          leads.new_leads.push({...this.props.all_leads_data[l]});
          break;
        case 'pending':
          leads.pending_leads.push({...this.props.all_leads_data[l]});
          break;
        case 'scheduled':
          leads.scheduled_leads.push({...this.props.all_leads_data[l]});
          break;
        case 'estimate':
          leads.estimate_leads.push({...this.props.all_leads_data[l]});
          break;
        case 'approved':
          leads.approved_leads.push({...this.props.all_leads_data[l]});
          break;
        case 'rejected':
          leads.rejected_leads.push({...this.props.all_leads_data[l]});
          break;
        default:
          leads.other_leads.push({...this.props.all_leads_data[l]});
      }
    }

    let sort_value = this.state.sort;


    for (let l in leads) {
      // leads[l].sort(function(a, b){
      //   var x = a.index;
      //   var y = b.index;
      //   if (x < y) {return -1;}
      //   if (x > y) {return 1;}
      //   return 0;
      // });
      leads[l].sort(function(a, b){
        var x = sort_value==="heat" ?
          Math.round(((a[a.status + "_date"])/1000/60/60/24)*8/max_heat[a.status] - new Date()) : a[sort_value].toLowerCase();
        var y = sort_value==="heat" ?
          Math.round(((b[b.status + "_date"])/1000/60/60/24)*8/max_heat[b.status] - new Date()) : b[sort_value].toLowerCase();
        if (x < y) {return -1;}
        if (x > y) {return 1;}
        return 0;
      });
      // leads[l].sort(function(a, b){
      //   var x = Math.round(((new Date() - a[a.status + "_date"])/1000/60/60/24)*8/max_heat[a.status]);
      //   var y = Math.round(((new Date() - b[b.status + "_date"])/1000/60/60/24)*8/max_heat[b.status]);
      //   if (x < y) {return 1;}
      //   if (x > y) {return -1;}
      //   return 0;
      // });
    }

    return (
      <div className="lead-kanban">
        <div className="menu">
          <div>
            <button
              onClick={()=>{
                this.props.selectLead("NEWLEAD");
              }}
              className=""
            >
              <i className="fas fa-plus"></i>
              <span>add a new lead</span>
            </button>
            <div className="select-container">
              <i className="fas fa-sort-alpha-down"></i>
              <select onChange={this.selectSort}>
                <option value="heat">sort by time in column</option>
                <option value="last_name">sort by last name</option>
                <option value="first_name">sort by first name</option>
              </select>
              <i className="fas fa-caret-down"></i>
            </div>
          </div>
        </div>
        <div className="columns">
        { this.props.all_leads_data && [
          (<div className="droppable-column" key="new">
            <h3><i className="fas fa-star-of-life"></i><span>new leads</span></h3>
            <div>
              <Droppable
                className="droppable-column"
                droppableId="new_leads"
                type="LEAD"
              >
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    style={getListStyle(snapshot.isDraggingOver)}>
                    {
                      leads.new_leads.map((item, index) => (
                        this.createLead(item, index)
                      ))
                    }
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </div>
          </div>),
          (<div className="droppable-column" key="scheduled">
            <h3><i className="far fa-calendar-alt"></i><span>estimate scheduled</span></h3>
            <div>
              <Droppable
                className="droppable-column"
                droppableId="scheduled_leads"
                type="LEAD"
              >
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    style={getListStyle(snapshot.isDraggingOver)}>
                    {
                      leads.scheduled_leads.map((item, index) => (
                        this.createLead(item, index)
                      ))
                    }
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </div>
          </div>),
          (<div className="droppable-column" key="pending">
            <h3><i className="fas fa-clock"></i><span>estimate pending</span></h3>
            <div>
              <Droppable
                className="droppable-column"
                droppableId="pending_leads"
                type="LEAD"
              >
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    style={getListStyle(snapshot.isDraggingOver)}>
                    {
                      leads.pending_leads.map((item, index) => (
                        this.createLead(item, index)
                      ))
                    }
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </div>
          </div>),
          (<div className="droppable-column" key="estimate">
            <h3><i className="fas fa-clipboard"></i><span>estimate sent</span></h3>
            <div>
              <Droppable
                className="droppable-column"
                droppableId="estimate_leads"
                type="LEAD"
              >
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    style={getListStyle(snapshot.isDraggingOver)}>
                    {
                      leads.estimate_leads.map((item, index) => (
                        this.createLead(item, index)
                      ))
                    }
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </div>
          </div>),
          (<div className="droppable-column" key="approved">
            <h3><i className="fas fa-user-check"></i><span>estimate approved</span></h3>
            <div>
              <Droppable
                className="droppable-column"
                droppableId="approved_leads"
                type="LEAD"
              >
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    style={getListStyle(snapshot.isDraggingOver)}>
                    {
                      leads.approved_leads.map((item, index) => (
                        this.createLead(item, index)
                      ))
                    }
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </div>
          </div>),
          (<div className="droppable-column" key="rejected">
            <h3><i className="fas fa-user-times"></i><span>estimate rejected</span></h3>
            <div>
              <Droppable
                className="droppable-column"
                droppableId="rejected_leads"
                type="LEAD"
              >
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    style={getListStyle(snapshot.isDraggingOver)}>
                    {
                      leads.rejected_leads.map((item, index) => (
                        this.createLead(item, index)
                      ))
                    }
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </div>
          </div>),
          (<div className="droppable-column" key="results">
            <h3><i className="fas fa-arrow-right"></i>continue</h3>
            <div className="continue-slots">
              <Droppable
                className="droppable-column"
                droppableId="project_leads"
                type="LEAD"
              >
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    style={getListStyle(snapshot.isDraggingOver)}>

                    {(<div className="drag-placeholder">
                      <i className="fas fa-toolbox"></i>
                      <div>start project from lead</div>
                      <div style={{display:"none"}}>{provided.placeholder}</div>
                    </div>)}
                  </div>
                )}
              </Droppable>
              <Droppable
                className="droppable-column"
                droppableId="archive_leads"
                type="LEAD"
              >
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    style={getListStyle(snapshot.isDraggingOver)}>

                    {(<div className="drag-placeholder">
                      <i className="fas fa-archive"></i>
                      <div>archive lead</div>
                      <div style={{display:"none"}}>{provided.placeholder}</div>
                    </div>)}
                  </div>
                )}
              </Droppable>
            </div>
          </div>)
        ]}
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    all_leads_data: state.state.all_leads_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({updateLeadsData, selectLead, openProjectEditor, updateProjectsData, updateUsersData, selectJob}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(LeadKanban);
