import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {UsersById} from '../utilities/data'

import {SplitString} from '../utilities/string'
import {ObjectToArray} from '../utilities/array'

import {updateUsersData, selectUser} from '../actions/actions';

class UserContractorList extends React.Component {
  state = {
    trade_filter: "all",
    trade_names: [],
    sort: "last_name"
  }

  callAPI () {
    let trades = {};
    for (var i in UsersById) {
      trades[SplitString(UsersById[i].trade_name,",")] = !!SplitString(UsersById[i].trade_name,",");
    }
    let trade_names = Object.keys(trades);
    trade_names.sort(function(a, b){
      var x = a.toLowerCase();
      var y = b.toLowerCase();
      if (x < y) {return -1;}
      if (x > y) {return 1;}
      return 0;
    });
    this.setState({
      trades: trades,
      trade_names: trade_names,
      trade_filter: "all",
      loaded: true
    });

    this.props.updateUsersData(UsersById);

  }

  componentDidMount() {
    if (this.props.all_users_data && !this.props.all_users_data[0]) {
      this.callAPI();
    }
  }


  selectSort = (e) => {
    this.setState({sort: e.target.value});
  }

  selectTradeFilter = (e) => {
    this.setState({trade_filter: e.target.value});
  }

  handleUserClick = (id) => {
    return (_e) => {
      this.props.selectUser(id);
    }
  }

  render() {
    let user_nodes = [];
    let users_sorted = ObjectToArray(this.props.all_users_data);
    let trades = [];
    for (var t=0; t<this.state.trade_names.length; t++) {
      trades.push((
        <option key={this.state.trade_names[t]} value={this.state.trade_names[t]}>{this.state.trade_names[t]}</option>
      ));
    }

    const sort_value = this.state.sort;
    const trade_value = this.state.trade_filter;

    users_sorted.sort(function(a, b){
      var x = a[sort_value] ? (a.trade_name.indexOf(trade_value)===0 ? "a" : a[sort_value].toLowerCase()) : "zzzzzz";
      var y = b[sort_value] ? (b.trade_name.indexOf(trade_value)===0 ? "a" : b[sort_value].toLowerCase()) : "zzzzzz";
      if (x < y) {return -1;}
      if (x > y) {return 1;}
      return 0;
    });

    for (var u in users_sorted) {
      let user = users_sorted[u];
      if (user.trade_name.indexOf("Contractor")+1) {
        if (this.state.trade_filter === "all" || (user.trade_name.indexOf(this.state.trade_filter)+1)){
          let role_nodes = [];
          if (user.roles) {
            for (var r=0; r<user.roles.length; r++) {
              role_nodes.push((
                <span key={this.props.roles[user.roles[r]].name}>{this.props.roles[user.roles[r]].name}</span>
              ))
            }
          }

          user_nodes.push ((
            <li key={user.id}
              onClick={this.handleUserClick(user.id)}
            >
              {user.photo_uri ? (
                <img src={user.photo_uri} alt={user.first_name + " " + user.last_name} />
              ):(
                <i className="fas fa-user"></i>
              )}
              <div className="info">
                <h3>{user.first_name + " " + user.last_name}</h3>
                <p>{user.email}</p>
              </div>
              <div className="roles">
                {role_nodes}
              </div>
            </li>
          ));
        }
      }
    }
    return (
      <div className={"user-list"}>
        <header>
          <div>
            <h2>Contractors</h2>
            <input id="filter-selects" type="checkbox" />
            <label className="filter-selects-button" htmlFor="filter-selects">
              <i className="fas fa-sort-amount-up"></i>
            </label>
            <div className="filter-selects">
              <div className="select-container">
                <i className="fas fa-sort-alpha-down"></i>
                <select onChange={this.selectSort}>
                <option value="last_name">sort by last name</option>
                <option value="first_name">sort by first name</option>
                  <option value="trade_name">sort by trade</option>
                </select>
                <i className="fas fa-caret-down"></i>
              </div>

              <div className="select-container">
                <i className="fas fa-filter"></i>
                <select value={this.state.trade_filter} onChange={this.selectTradeFilter}>
                  <option value="all">all</option>
                  {trades}
                </select>
                <i className="fas fa-caret-down"></i>
              </div>
            </div>
          </div>
          <div>
            <button
              onClick={()=>{
                this.props.selectUser("NEWUSER");
              }}
              className=""
            >
              <i className="fas fa-plus"></i>
              <span>add a new user</span>
            </button>
          </div>
        </header>
        <ul>
          {user_nodes}
        </ul>
      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    all_users_data: state.state.all_users_data,
    roles: state.state.roles
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({updateUsersData, selectUser}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(UserContractorList);
