import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

// import Editor from './editor';
import DatePicker from 'react-datepicker';

import {openInvoiceEditor, editInvoice, editProject} from '../actions/actions';



class InvoiceEditor extends React.Component {
  constructor(props) {
    super(props);
    this.input_refs = {};
    this.state = {
      // edit: this.props.edit || false
      invoice_data: {},
      project_data: {}
    };
  }

  componentDidUpdate(prevProps) {
    if (this.props.open_invoice_editor) {
      if (this.props.open_invoice_editor !== prevProps.open_invoice_editor) {
        // this.callAPI();
        this.setState({
          invoice_data: this.props.all_invoices_data[this.props.open_invoice_editor.invoice_id] || {
            id: "invoice"+ Math.random(),
            terms:"receipt",
            project_id: this.props.open_invoice_editor.project_id,
            project_address: this.props.all_projects_data[this.props.open_invoice_editor.project_id].address,
            billing_address: this.props.all_users_data[this.props.all_projects_data[this.props.open_invoice_editor.project_id].customer_id].billing_address,
            items: []
          },
          project_data: this.props.all_projects_data[this.props.open_invoice_editor.project_id] || {
            id: Math.random()
          }
        });
      }
    }
  }

  handleCloseButton = (e) => {
    this.props.openInvoiceEditor(null);
  }

  handleChange = (e) => {
    this.setState({
      invoice_data: {...this.state.invoice_data, [e.target.name]:e.target.value}
    });
  }

  handleDescriptionChange = (value) => {
    this.setState({
      invoice_data: {...this.state.invoice_data, description:value}
    });
  }

  handleIconChange = (changeEvent) => {
    this.setState({
      icon: changeEvent.target.value
    });
    setTimeout(()=>{
      this.setState({
        invoice_data: {}
      });
    },500);
  };

  handleSaveChanges = (e) => {
    this.props.editInvoice(this.state.invoice_data.id, this.state.invoice_data);
    this.props.editProject(this.props.open_invoice_editor.project_id, {invoice_id: this.state.invoice_data.id});

    console.log(this.props.open_invoice_editor.project_id);

    setTimeout(()=>{
      this.setState({
        invoice_data: {}
      });
    },500);
  }

  handleItemProductChange = (i, prop) => {
    return (e) => {
      let items = [...this.state.invoice_data.items];

      if (items[i]) {
        items[i][prop] = e.target.value;
      } else {
        items.push({
          [prop]: e.target.value
        })
      }

      this.setState({
        invoice_data: {...this.state.invoice_data, items: items}
      });
    }
  }

  selectTerms = (e) => {
    this.setState({
      invoice_data: {...this.state.invoice_data, terms:e.target.value}
    });
  }

  handleKeyPress = (i, type)=>{
    return (e) => {
      if(e.key === 'Enter'){
        const input = document.getElementById("invoice"+(i+1)+type);
        console.log(input);
        if (input) {
          input.focus();
        }
      }
    }
  }

  handleDelete = (id) => {
    return (e)=>{
      let items = [...this.state.invoice_data.items];

      items.splice(id, 1);

      this.setState({
        invoice_data: {...this.state.invoice_data, items: items}
      });
    }
  }

  render() {
    if (this.props.all_projects_data) {
      let project_manager = "no manager";
      const project = this.state.project_data;
      const customer = this.props.all_users_data[this.state.project_data.customer_id] || {};

      if (this.props.open_invoice_editor && this.props.open_invoice_editor.project_id) {

        if (this.props.all_users_data[project.project_manager]) {
          project_manager = this.props.all_users_data[project.project_manager].first_name + " " + this.props.all_users_data[project.project_manager].last_name;
        }
      }

      let terms_nodes = [];

      for (let t in this.props.terms) {
        terms_nodes.push((
          <option key={this.props.terms[t].id} value={this.props.terms[t].id}>{this.props.terms[t].name}</option>
        ));
      }

      let table_rows = [];
      let total = 0;
      if (this.state.invoice_data.items) {
        for (var i=0; i<this.state.invoice_data.items.length+1; i++) {
          let item = this.state.invoice_data.items[i] || {};
          const amount = (parseInt(item.quantity, 10) * parseInt(item.rate, 10));
          table_rows.push((
            <tr key={i} className={i < this.state.invoice_data.items.length ? "" : "new"}>
              <td>
                {i < this.state.invoice_data.items.length && (<button className="icon-button" onClick={this.handleDelete(i)} title="Delete Task"><i className="fas fa-trash"></i></button>)}
              </td>
              <td>{i+1}</td>
              <td><input
                type="text"
                onKeyPress={this.handleKeyPress(i, "product")}
                id={"invoice"+i+"product"}
                value={item.product || ""}
                onChange={this.handleItemProductChange(i, "product")}
              /></td>
              <td><input
                type="text"
                onKeyPress={this.handleKeyPress(i, "description")}
                id={"invoice"+i+"description"}
                value={item.description || ""}
                onChange={this.handleItemProductChange(i, "description")}
              /></td>
              <td><input
                onKeyPress={this.handleKeyPress(i, "quantity")}
                id={"invoice"+i+"quantity"}
                type="number" min="1"
                value={item.quantity || ""}
                onChange={this.handleItemProductChange(i, "quantity")}
              /></td>
              <td><input
                onKeyPress={this.handleKeyPress(i, "rate")}
                id={"invoice"+i+"rate"}
                type="number" min="0"
                value={item.rate || ""}
                onChange={this.handleItemProductChange(i, "rate")}
              /></td>
              <td className="value">{i < this.state.invoice_data.items.length && (amount || "0")}</td>
            </tr>
          ));
          total += (amount || 0);
        }
      }

      return (
        <div className={"invoice-editor form" + (this.props.open_invoice_editor && this.props.all_projects_data?" show":"")}>
          <div className="content">
            <header>
              <h1>
                <span>{(project.name || "")}</span>
              </h1>
              <div>
                <button
                  onClick={()=>{
                    this.handleSaveChanges();
                    this.handleCloseButton();
                  }}
                  className=""
                >
                  <i className="fas fa-check"></i>
                  <span>save</span>
                </button>
                <button
                  onClick={()=>{
                    this.handleCloseButton();
                  }}
                  className=""
                >
                  <i className="fas fa-times"></i>
                  <span>cancel</span>
                </button>
              </div>
            </header>
            <main>
              <div>
                <div>
                  <label>project manager</label>
                  <p>{project_manager}</p>
                </div>
                <div>
                  <label>invoice number</label>
                  <p>{this.state.invoice_data.invoice_number}</p>
                </div>
              </div>
              <div>
                <div>
                  <label>project address</label>
                  <textarea name="project_address" placeholder="project address" value={this.state.invoice_data.project_address || ""} onChange={this.handleChange} />
                </div>
              </div>
              <div>
                <div>
                  <label>bill to</label>
                  <p>{customer.first_name + " " + customer.last_name}</p>
                </div>
              </div>
              <div>
                <div>
                  <label>billing address</label>
                  <textarea name="billing_address" placeholder="billing address" value={this.state.invoice_data.billing_address || ""} onChange={this.handleChange} />
                </div>
              </div>
              <div>
                <div>
                  <label>terms</label>
                  <div className="select-container">
                    <select value={this.state.invoice_data.terms} onChange={this.selectTerms}>

                      {terms_nodes}
                    </select>
                    <i className="fas fa-caret-down"></i>
                  </div>
                </div>
              </div>
              <div>
                <div>
                  <label>invoice date</label>
                  <DatePicker
                    selected={this.state.invoice_data.invoice_date ? new Date(this.state.invoice_data.invoice_date) : new Date()}
                    className=""
                    onChange={this.handleChangeInvoieDate}
                  />
                </div>
                <div>
                  <label>invoice expiration date</label>
                  <DatePicker
                    selected={this.state.invoice_data.invoice_date ? new Date(this.state.invoice_data.expiration_date) : new Date()}
                    className=""
                    onChange={this.handleChangeInvoieDate}
                  />
                </div>
                <div>
                  <label>payment due date</label>
                  <DatePicker
                    selected={this.state.invoice_data.invoice_date ? new Date(this.state.invoice_data.expiration_date) : new Date()}
                    className=""
                    onChange={this.handleChangeInvoieDate}
                  />
                </div>
              </div>

              <div>
                <div>
                  <table>
                    <tbody>
                      <tr>
                        <th></th>
                        <th>#</th>
                        <th>product/service</th>
                        <th>description</th>
                        <th>quantity</th>
                        <th>rate</th>
                        <th>amount</th>
                      </tr>
                      {table_rows}
                    </tbody>
                  </table>
                </div>
              </div>


              <div>
                <div>
                  <label>message displayed on estimate</label>
                  <textarea name="message" placeholder="message" value={this.state.invoice_data.message || ""} onChange={this.handleChange} />
                </div>
                <div className="f">
                  <label>total</label>
                  <div className="value">{total}</div>
                </div>
              </div>
            </main>
          </div>
        </div>
      );
    } else {
      return ([]);
    }
  }
}


const mapStateToProps = (state) => {
  return {
    selected_job: state.state.selected_job,
    open_invoice_editor: state.state.open_invoice_editor,
    all_invoices_data: state.state.all_invoices_data,
    all_projects_data: state.state.all_projects_data,
    terms: state.state.terms,
    all_users_data: state.state.all_users_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({openInvoiceEditor, editInvoice, editProject}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(InvoiceEditor);
