import React from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { updateLeadsData } from '../actions/actions';

import AdvancedSettings from './advanced-settings';
import UserList from './user-list';
import UserContractorList from './user-contractor-list';
import UserEditor from './user-editor';


class PageAdmin extends React.Component {
  state = {
    loaded: false,
    tab: "user"
  };
  componentDidMount() {
    this.temp_load_data = setTimeout(()=>{
      this.setState({loaded: true});
    }, 1000);
  }

  componentWillUnmount () {
    window.clearTimeout(this.temp_load_data);
  }

  changeMode (mode) {
    this.setState({
      tab: mode
    });
  }

  render() {
    return (
      <section id="page-admin" className={this.state.loaded?"loaded":"loading"}>
        <div className="load-background"></div>
        <div className="card f-nogrow">
          <div className="admin-panel">
            <header>
              <div>
                <h1>Admin Panel</h1>
                <button className={"tab-button " + (this.state.tab === "user"?"selected":"")} onClick={()=>{this.changeMode("user")}}>add/edit users</button>
                <button className={"tab-button " + (this.state.tab === "contractor"?"selected":"")} onClick={()=>{this.changeMode("contractor")}}>add/edit contractors</button>
                <button className={"tab-button " + (this.state.tab === "advanced"?"selected":"")} onClick={()=>{this.changeMode("advanced")}}>advanced settings</button>

              </div>
            </header>
            <div className="tab-panel">
              <div className={this.state.tab === "user"?"show":""}>
                { this.state.tab === "user" && (
                  <UserList />
                )}
              </div>
              <div className={this.state.tab === "contractor"?"show":""}>
                { this.state.tab === "contractor" && (
                  <UserContractorList />
                )}
              </div>
              <div className={this.state.tab === "advanced"?"show":""}>
                { this.state.tab === "advanced" && (
                  <AdvancedSettings />
                )}
              </div>
            </div>
          </div>
        </div>
        <UserEditor />
      </section>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    all_leads_data: state.state.all_leads_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({updateLeadsData}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(PageAdmin);
