import React from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { updateLeadsData } from '../actions/actions';

import LeadKanban from './lead-kanban';
import LeadChart from './lead-chart';
import ProjectEditor from './project-editor';

import {LeadList} from '../utilities/data'

import LeadEstimator from './lead-estimator';

class PageLeads extends React.Component {
  state = {
    loaded: false
  };
  componentDidMount() {
    this.temp_load_data = setTimeout(()=>{
      const lead_names = Object.keys(this.props.all_leads_data);
      if (!lead_names.length) {
        this.props.updateLeadsData(LeadList);
      }
      this.setState({loaded: true});
    }, 1000);
  }

  componentWillUnmount () {
    window.clearTimeout(this.temp_load_data);
  }

  render() {
    return (
      <section id="page-leads" className={this.state.loaded?"loaded":"loading"}>
        <div className="load-background"></div>
        <div className="card f-nogrow">
          <LeadChart />
        </div>
        <div className="card d1 f-grow kanban-card">
          <LeadKanban />
        </div>
        <LeadEstimator />
      </section>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    all_leads_data: state.state.all_leads_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({updateLeadsData}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(PageLeads);
