import React from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {
  updateProjectTemplatesData,
  updateJobTemplatesData,
  editLead,
  openJobEditor
} from '../actions/actions';


import {ProjectTemplates, JobTemplates} from '../utilities/data'

import {Droppable, Draggable } from 'react-beautiful-dnd';


const getProjectItemStyle = (item, isDragging, draggableStyle) => {
  return {
    // some basic styles to make the items look a bit nicer
    color: "black",
    background: "#c1c1c1",
    padding: "1rem",
    margin: "0 .5rem .5rem 0",
    borderRadius: "5px",
    width: "auto",
    // change background colour if dragging
    // background: heat_map[heat],//isDragging ? 'lightgreen' : 'grey',
    // boxShadow: "rgba(0, 0, 0, 0.12) 0 0 1rem",
    // styles we need to apply on draggables
    ...draggableStyle
  }
};

const getItemStyle = (item, isDragging, draggableStyle) => {
  return {
    // some basic styles to make the items look a bit nicer
    color: "black",
    background: "#c1c1c1",
    padding: "1rem",
    margin: "0 .5rem .5rem 0",
    borderRadius: "5px",
    width: "auto",
    // change background colour if dragging
    // background: heat_map[heat],//isDragging ? 'lightgreen' : 'grey',
    // boxShadow: "rgba(0, 0, 0, 0.12) 0 0 1rem",
    // styles we need to apply on draggables
    ...draggableStyle
  }
};

const getTimelineItemStyle = (item, isDragging, draggableStyle) => {
  return {
    // some basic styles to make the items look a bit nicer
    padding: ".5rem",
    margin: "0 .5rem 0 0",
    borderRadius: "5px",
    width: "auto",
    flex: "0 0 auto",
    backgroundColor: "#3a3a3a",
    // change background colour if dragging
    // background: heat_map[heat],//isDragging ? 'lightgreen' : 'grey',
    // boxShadow: "rgba(0, 0, 0, 0.12) 0 0 1rem",
    // styles we need to apply on draggables
    ...draggableStyle
  }
};

const getListStyle = isDraggingOver => ({
    background: isDraggingOver ? '#e4e4e4' : 'transparent',
    display: "flex",
    padding: ".5rem 0",
    width: "100%",
    minHeight: "5rem",
    flex: "1 1 auto",
    position: "relative",
    zIndex: "1",
    flexFlow: "row wrap"
});

const getDropListStyle = isDraggingOver => ({
    background: isDraggingOver ? '#e4e4e4' : 'transparent',
    display: "flex",
    padding: "1.5rem",
    width: "100%",
    minHeight: "10rem",
    flex: "1 1 auto",
    position: "relative",
    zIndex: "1"
});


class EstimateProject extends React.Component {
  state = {
    loaded: false,
    tab: "project-templates"
  };

  getTemplateData () {
    if (!this.props.all_job_templates_data || !this.props.all_project_templates_data) {
      this.props.updateJobTemplatesData(JobTemplates);
      this.props.updateProjectTemplatesData(ProjectTemplates);
    }
  }

  componentDidMount() {
    this.getTemplateData();
  }

  handleTimelineValueChange = (item, index, input) => {
    return (e) => {
      let jobs = [...this.props.all_leads_data[this.props.selected_lead].jobs];
      jobs[index].data[input].value = e.target.value;
      this.props.editLead(this.props.selected_lead, {jobs:jobs});
    };
  }

  handleDeleteJobTemplate = (index) => {
    return (e) => {
      e.stopPropagation();
      let jobs = [...this.props.all_leads_data[this.props.selected_lead].jobs];
      jobs.splice(index, 1);
      console.log(index);
      this.props.editLead(this.props.selected_lead, {jobs:jobs});
    }
  }

  handleTimelineJobClick = (item, index) => (
    (_e) => {
      this.props.openJobEditor({
        lead_id: this.props.selected_lead,
        job_data: item,
        job_index: index
      });
    }
  )

  handleLabelClick = (e) => {
    e.stopPropagation();
  }

  createTimelineJob = (id, index) => {
    var inputs = [];
    let item = this.props.all_jobs_data[id];
    if (item.data) {
      for (var i = 0; i<item.data.length; i++) {
        inputs.push((
          <div key={item.id + i}>
            <label onClick={this.handleLabelClick}>
              <span>{item.data[i].name}</span>
              <input type={item.data[i].type} value={item.data[i].value} onChange={this.handleTimelineValueChange(item, index, i)} />
            </label>
          </div>
        ));
      }
    }
    return (<Draggable
      key={"timeline_"+item.id+index}
      draggableId={"timeline_"+item.id+index}
      index={index}>
      {(provided, snapshot) => (
        <div
          ref={provided.innerRef}
          {...provided.draggableProps}
          {...provided.dragHandleProps}
          onClick={this.handleTimelineJobClick(item, index)}
          style={getTimelineItemStyle(
            item,
            snapshot.isDragging,
            provided.draggableProps.style
          )}>
          <header>
            <i className={item.icon}></i>
            <span className="title">{item.name}</span>
            <button title="close" onClick={this.handleDeleteJobTemplate(index)} className="icon-button"><i className="fas fa-times"></i></button>
          </header>
          {inputs}
        </div>
      )}
    </Draggable>);
  }

  createJob = (item, index) => {
    return (<Draggable
      key={item.id}
      draggableId={item.id}
      index={index}>
      {(provided, snapshot) => (
        <div
          className="job"
          ref={provided.innerRef}
          {...provided.draggableProps}
          {...provided.dragHandleProps}

          style={getItemStyle(
            item,
            snapshot.isDragging,
            provided.draggableProps.style
          )}>

          <i className={item.icon}></i>
          <span>{item.name}</span>
        </div>
      )}
    </Draggable>);
  }

  createProject = (item, index) => {
    return (<div className="drag-project" key={item.id}>
      <Draggable
        key={item.id}
        draggableId={item.id}
        index={index}
      >
        {(provided, snapshot) => (
          <div
            ref={provided.innerRef}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
            style={getProjectItemStyle(
              item,
              snapshot.isDragging,
              provided.draggableProps.style
            )}>
            <div>{item.name}</div>
          </div>
        )}
      </Draggable>
      {this.props.all_job_templates_data && (
        <div className="jobs">{
          item.jobs.map((job, index) => (
            <div key={job}>
              <i className={this.props.all_job_templates_data[job].icon}></i>
              {this.props.all_job_templates_data[job].name}
            </div>
          ))
        }</div>
      )}
    </div>);
  }


  changeMode (mode) {
    this.setState({
      tab: mode
    });
  }

  render() {
    let timeline = this.props.selected_lead ? this.props.all_leads_data[this.props.selected_lead].jobs : [];
    let job_templates = [];
    let project_templates = [];
    const sort_value = "name";



    if (this.props.all_job_templates_data) {
      for (var j in this.props.all_job_templates_data) {
        job_templates.push(this.props.all_job_templates_data[j]);
      }

      job_templates.sort(function(a, b){
        var x = a[sort_value].toLowerCase();
        var y = b[sort_value].toLowerCase();
        if (x < y) {return -1;}
        if (x > y) {return 1;}
        return 0;
      });
    }

    if (this.props.all_project_templates_data) {
      for (var p in this.props.all_project_templates_data) {
        project_templates.push(this.props.all_project_templates_data[p]);
      }

      project_templates.sort(function(a, b){
        var x = a[sort_value].toLowerCase();
        var y = b[sort_value].toLowerCase();
        if (x < y) {return -1;}
        if (x > y) {return 1;}
        return 0;
      });
    }

    return (
      <article
        className={"estimate-project"}
      >
        <header>
          <h2>{this.props.selected_lead_data.name}</h2>
        </header>
        <div className="timeline">
          <Droppable
            className="droppable-column"
            droppableId="timeline"
            type="TEMPLATE"
            direction="horizontal"
          >
            {(provided, snapshot) => (
              <div
                ref={provided.innerRef}
                style={getDropListStyle(snapshot.isDraggingOver)}>
                {!timeline.length && (
                  <span className="icon">
                    <span>drag templates </span>
                    <i className="fas fa-hand-rock"></i>
                    <span> on to timeline</span>
                  </span>
                )}
                {
                  timeline.map((item, index) => (
                    this.createTimelineJob(item, index)
                  ))
                }
                {provided.placeholder}
                <div style={{
                  width: "1rem",
                  opacity: "0",
                  display: "inline-block",
                  height: "2rem",
                  flex: "0 0 auto"
                }}></div>
              </div>
            )}

          </Droppable>
        </div>

        <div className="job-templates">
          <header>
            <button className={"tab-button " + (this.state.tab === "project-templates"?"selected":"")} onClick={()=>{this.changeMode("project-templates")}}>project templates</button>
            <button className={"tab-button " + (this.state.tab === "job-templates"?"selected":"")} onClick={()=>{this.changeMode("job-templates")}}>job templates</button>
          </header>
          <div className="tab-panel">
            <div className={this.state.tab === "project-templates"?"show":""}>
              {this.state.tab === "project-templates" && (
                <Droppable
                  className="droppable-column"
                  droppableId="template_jobs"
                  type="TEMPLATE"
                  isDropDisabled
                >
                  {(provided, snapshot) => (
                    <div
                      ref={provided.innerRef}
                      style={getListStyle(snapshot.isDraggingOver)}>
                      {
                        project_templates.map((item, index) => (
                          this.createProject(item, index)
                        ))
                      }
                    </div>
                  )}
                </Droppable>
              )}
            </div>
            <div className={this.state.tab === "job-templates"?"show":""}>
              {this.state.tab === "job-templates" && (
                <Droppable
                  className="droppable-column"
                  droppableId="template_jobs"
                  type="TEMPLATE"
                  direction="horizontal"
                  isDropDisabled
                >
                  {(provided, snapshot) => (
                    <div
                      ref={provided.innerRef}
                      style={getListStyle(snapshot.isDraggingOver)}>
                      {
                        job_templates.map((item, index) => (
                          this.createJob(item, index)
                        ))
                      }
                    </div>
                  )}
                </Droppable>
              )}
            </div>
          </div>
        </div>
      </article>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    selected_lead: state.state.selected_lead,
    all_leads_data: state.state.all_leads_data,
    all_jobs_data: state.state.all_jobs_data,
    all_project_templates_data: state.state.all_project_templates_data,
    all_job_templates_data: state.state.all_job_templates_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    updateProjectTemplatesData,
    updateJobTemplatesData,
    editLead,
    openJobEditor
  }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(EstimateProject);
