import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import Editor from './editor';

import LeadTaskList from './lead-task-list';

import {openJobEditor, editJob, editLead} from '../actions/actions';


class JobEditor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // edit: this.props.edit || false
      job_data: {
        id: Math.random()
      }
    };
  }

  componentDidUpdate(prevProps) {
    if (this.props.open_job_editor && this.props.open_job_editor.lead_id) {
      if (!prevProps.open_job_editor || this.props.open_job_editor.lead_id !== prevProps.open_job_editor.lead_id) {
        this.setState({
          job_data: {...this.props.open_job_editor.job_data}
        });
      }
    }
    else if (this.props.open_job_editor) {
      if (this.props.open_job_editor !== prevProps.open_job_editor) {
        if (this.props.open_job_editor === "NEWJOB") {

        }
        else {
          this.setState({
            job_data: this.props.all_jobs_data[this.props.open_job_editor] || {}
          });
        }
      }
    }
  }

  handleCloseButton = (e) => {
    this.props.openJobEditor(null);
  }

  handleChange = (e) => {
    this.setState({
      job_data: {...this.state.job_data, [e.target.name]:e.target.value}
    });
  }

  handleDescriptionChange = (value) => {
    this.setState({
      job_data: {...this.state.job_data, description:value}
    });
  }

  handleSaveChanges = (e) => {
    if (this.props.open_job_editor.lead_id) {

      let jobs = [...this.props.all_leads_data[this.props.open_job_editor.lead_id].jobs];

      // jobs[this.props.open_job_editor.job_index] = {...this.state.job_data};

      // this.props.editLead(this.props.open_job_editor.lead_id, {jobs:jobs});
      this.props.editJob(jobs[this.props.open_job_editor.job_index], this.state.job_data);

    } else {
      if (this.props.open_job_editor === "NEWJOB") {
        this.props.updateJobData({[this.state.job_data.id]: this.state.job_data});
      } else {
        this.props.editJob(this.state.job_data.id, this.state.job_data);
      }
    }
    setTimeout(()=>{
      this.setState({
        job_data: {}
      });
    },500);
  }

  render() {

    return (
      <div className={"job-editor form" + (this.props.open_job_editor && this.props.all_jobs_data?" show":"")}>
        <div className="content">
          <header>
            <h1>
              <span>{(this.state.job_data.name || "")}</span>
            </h1>
            <div>
              <button
                onClick={()=>{
                  this.handleSaveChanges();
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-check"></i>
                <span>save</span>
              </button>
              <button
                onClick={()=>{
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-times"></i>
                <span>cancel</span>
              </button>
            </div>
          </header>
          <main>
            <div>
              <div>
                <div>
                  <label>job name</label>
                  <input name="name" type="text" placeholder="job name" value={this.state.job_data.name || ""} onChange={this.handleChange} />
                </div>
                <div>
                  <label>notes</label>
                  <Editor
                    defaultValue={
                      ((this.props.open_job_editor && this.props.open_job_editor.lead_id) ?(
                        this.props.open_job_editor.job_data.description
                      ):(
                        this.props.all_jobs_data[this.props.open_job_editor] && this.props.all_jobs_data[this.props.open_job_editor].description
                      ))
                      || ""
                    }
                    onChange={this.handleDescriptionChange}
                    refresh={this.state.job_data.id}
                  />
                </div>
              </div>



            </div>
          </main>
        </div>
      </div>
    );
  }
}

// <div>
//   <LeadTaskList />
// </div>


const mapStateToProps = (state) => {
  return {
    selected_job: state.state.selected_job,
    open_job_editor: state.state.open_job_editor,
    new_job_data: state.state.new_job_data,
    all_jobs_data: state.state.all_jobs_data,
    all_leads_data: state.state.all_leads_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({openJobEditor, editJob, editLead}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(JobEditor);
