import React from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { selectJob, selectJobSchedule, updateJobsData, editJob, assignJobToUser } from '../actions/actions';

import DatePicker, {
  CalendarContainer as OriginalContainer
} from 'react-datepicker';

// import UserDaySchedule from './user-day-schedule'

import {DaysInMonth, SameDay} from '../utilities/date'
import {JobsById} from '../utilities/data'

class UserHeatmap extends React.Component {

  calcJobsByDay () {
    var jobs_by_day = [];
    for (var d=0; d<DaysInMonth(this.props.month, this.props.year); d++) {
      jobs_by_day.push([]);
    }

    for (var t=0; t<this.props.all_users_data[this.props.user_id].jobs.length; t++) {
      const job_id = this.props.all_users_data[this.props.user_id].jobs[t];
      let job = this.props.all_jobs_data[job_id];
      if (!job) {
        this.props.updateJobsData(JobsById);
        return jobs_by_day;
      }
      if (job.start_dates.length) {
        job.start_dates.map((start_date, i) => {
          if (start_date.getMonth() === this.props.month) {
            jobs_by_day[start_date.getDate()-1].push({...job, index:i, start_date: start_date, end_date: job.end_dates[i]});
          }
          return true;
        });
      }
    }
    return jobs_by_day;
  }

  handleDayCellClick = (hour) => {
    return (e) => {
      if (this.props.selected_job_data && !this.props.all_jobs_data[this.props.selected_job].start_dates.length) {
        // this.props.selectJobSchedule(new Date(this.props.year, this.props.month, (this.props.selected_day)), this.props.user_id);
        let start_date = new Date(this.props.year, this.props.month, (this.props.selected_day), hour);
        let end_date = new Date(this.props.year, this.props.month, (this.props.selected_day), hour+1);

        let start_dates = [...this.props.all_jobs_data[this.props.selected_job].start_dates];
        let end_dates = [...this.props.all_jobs_data[this.props.selected_job].end_dates];

        start_dates[this.props.selected_job_data.index] = start_date;
        end_dates[this.props.selected_job_data.index] = end_date;

        this.props.editJob(this.props.selected_job, {start_dates:start_dates, end_dates:end_dates});
        this.props.assignJobToUser(this.props.all_jobs_data[this.props.selected_job].assigned_user, this.props.user_id, this.props.selected_job);
      }
    }
  }
  handleDayClick = (id, index) => {
    return(_e) => {
      this.props.handleDayClick(id, index);
    }
  }
  handleJobClick = (project_id, id, index) => {
    return (_e) => {
      this.props.selectJob(project_id, id, index);
      // this.props.selectJobSchedule(new Date(this.props.year, this.props.month, (this.props.selected_day)), this.props.user_id);
    }
  }


  handleChangeStartTime = (id, index) => {
    return (value) => {
      let start_dates = [...this.props.all_jobs_data[id].start_dates];
      let end_dates = [...this.props.all_jobs_data[id].end_dates];

      start_dates[index] = value;
      end_dates[index] = new Date(end_dates[index].setFullYear(value.getFullYear(),value.getMonth(),value.getDate()));
      console.log(value, end_dates[index]);

      this.props.editJob(id, {start_dates:start_dates, end_dates:end_dates});
      if (id === this.props.selected_job) {
        this.props.gotoDate(value);
      }
    }
  }

  handleChangeEndTime = (id, index) => {
    return (value) => {
      let end_dates = [...this.props.all_jobs_data[id].end_dates];

      end_dates[index] = value;

      this.props.editJob(id, {end_dates:end_dates});
    }
  }

  isDayAssigned = (current_date) => {
    let assigned = "";

    if (this.props.selected_job_data && this.props.selected_job_data.assigned_user === this.props.user_id && this.props.selected_job_data.start_dates.length) {
      this.props.selected_job_data.start_dates.map((start_date, i) => {
        if (SameDay(start_date, current_date)){
          assigned = " assigned";
        }
        return start_date;
      })
    }
    return assigned;
  }

  startx = 0;
  dist = 0;
  dragged_element = null;
  event_store = {};

  handleEndTouchStart = (id, index, job_duration) => {
    return (e) => {
      let touchobj = e.changedTouches[0]; // reference first touch point
      this.startx = parseInt(touchobj.clientX);
      this.dist = 0;
      // const job_duration = (((this.props.all_jobs_data[id].end_dates[index]-this.props.all_jobs_data[id].start_dates[index])/1000)/60/60/24)*100;
      const window_width = e.currentTarget.parentNode.parentNode.clientWidth;

      this.event_store["EndTouchMove"+id+index] = this.handleEndTouchMove(id, index, job_duration, window_width);
      this.event_store["EndTouchEnd"+id+index] = this.handleEndTouchEnd(id, index, job_duration, window_width);

      e.currentTarget.addEventListener('touchend', this.event_store["EndTouchEnd"+id+index], false);
      e.currentTarget.addEventListener('touchmove', this.event_store["EndTouchMove"+id+index], false);
      e.currentTarget.addEventListener('touchcancel', this.event_store["EndTouchEnd"+id+index], false);

    }
  }

  handleEndMouseDown = (id, index, job_duration) => {
    return (e) => {
      this.dragged_element = {
        id,
        index,
        job_duration,
        side:"end",
        window_width: e.currentTarget.parentNode.parentNode.clientWidth,
        e
      }
      this.dist = 0;
      this.startx = parseInt(e.clientX);
      document.addEventListener('mousemove', this.handleEndMouseMove, false);
      document.addEventListener('mouseup', this.handleEndMouseUp, false);
      e.preventDefault();
      e.stopPropagation();
    }
  }

  handleEndMouseMove = (e) => {
    const {id, index, job_duration, window_width} = this.dragged_element;
    this.dist = parseInt(e.clientX) - this.startx;

    document.getElementById("job-"+id+index).style.maxWidth = (job_duration + (this.dist*100 / window_width))+"%";
    document.getElementById("job-"+id+index).style.minWidth = (job_duration + (this.dist*100 / window_width))+"%";

    let end_dates = [...this.props.all_jobs_data[id].end_dates];
    let start_dates = [...this.props.all_jobs_data[id].start_dates];


    let start_hour = start_dates[index].getHours() + (start_dates[index].getMinutes() / 60);
    let hour = Math.max(end_dates[index].getHours() + (end_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24), start_hour);

    const min = Math.round((hour % 1) * 60);
    let hr = Math.floor(hour);
    let ampm = "am";
    if( hr > 12 ) {
        hr -= 12;
        ampm = "pm";
    }
    document.getElementById("job-end-tooltip-"+id+index).innerHTML = hr+":"+(min<10?"0"+min:min)+" "+ampm;

    e.stopPropagation();
    e.preventDefault();
  }

  handleEndMouseUp = (e) => {
    const {id, index, window_width} = this.dragged_element;
    let end_dates = [...this.props.all_jobs_data[id].end_dates];

    let hour = end_dates[index].getHours() + (end_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24);

    let value = new Date(new Date(end_dates[index]).setHours(Math.floor(hour), (hour % 1) * 60));

    end_dates[index] = value;
    this.props.editJob(id, {end_dates:end_dates});

    document.removeEventListener('mousemove', this.handleEndMouseMove, false);
    document.removeEventListener('mouseup', this.handleEndMouseUp, false);
  }

  handleEndTouchMove = (id, index, job_duration, window_width) => {
    return (e) => {
      let touchobj = e.changedTouches[0];
      this.dist = parseInt(touchobj.clientX) - this.startx;
      document.getElementById("job-"+id+index).style.maxWidth = (job_duration + (this.dist*100 / window_width))+"%";
      document.getElementById("job-"+id+index).style.minWidth = (job_duration + (this.dist*100 / window_width))+"%";

      let end_dates = [...this.props.all_jobs_data[id].end_dates];
      let start_dates = [...this.props.all_jobs_data[id].start_dates];

      let start_hour = start_dates[index].getHours() + (start_dates[index].getMinutes() / 60);
      let hour = Math.max(end_dates[index].getHours() + (end_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24), start_hour);

      const min = Math.round((hour % 1) * 60);
      let hr = Math.floor(hour);
      let ampm = "am";
      if( hr > 12 ) {
          hr -= 12;
          ampm = "pm";
      }
      document.getElementById("job-end-tooltip-"+id+index).innerHTML = hr+":"+(min<10?"0"+min:min)+" "+ampm;

      e.stopPropagation();
      e.preventDefault();
    }
  }

  handleEndTouchEnd = (id, index, job_duration, window_width) => {
    return (e) => {
      let end_dates = [...this.props.all_jobs_data[id].end_dates];

      let hour = end_dates[index].getHours() + (end_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24);

      let value = new Date(new Date(end_dates[index]).setHours(Math.floor(hour), (hour % 1) * 60));

      end_dates[index] = value;
      console.log(this.dist, hour, value);

      this.props.editJob(id, {end_dates:end_dates});

      e.currentTarget.removeEventListener('touchend', this.event_store["EndTouchEnd"+id+index], false);
      e.currentTarget.removeEventListener('touchmove', this.event_store["EndTouchMove"+id+index], false);
      e.currentTarget.removeEventListener('touchcancel', this.event_store["EndTouchEnd"+id+index], false);
      e.stopPropagation();
      e.preventDefault();
    }
  }

///////
  //////////////////BODY CLICK AND DRAG
//////

  handleBodyTouchStart = (id, index, job_start) => {
    return (e) => {
      let touchobj = e.changedTouches[0]; // reference first touch point
      this.dist = 0;
      this.startx = parseInt(touchobj.clientX);
      console.log("body drag");
      // const job_start = (((this.props.all_jobs_data[id].end_dates[index]-this.props.all_jobs_data[id].start_dates[index])/1000)/60/60/24)*100;
      const window_width = e.currentTarget.parentNode.parentNode.clientWidth;

      this.event_store["BodyTouchMove"+id+index] = this.handleBodyTouchMove(id, index, job_start, window_width);
      this.event_store["BodyTouchEnd"+id+index] = this.handleBodyTouchEnd(id, index, job_start, window_width);

      e.currentTarget.addEventListener('touchend', this.event_store["BodyTouchEnd"+id+index], false);
      e.currentTarget.addEventListener('touchmove', this.event_store["BodyTouchMove"+id+index], false);
      e.currentTarget.addEventListener('touchcancel', this.event_store["BodyTouchEnd"+id+index], false);

    }
  }

  handleBodyTouchMove = (id, index, job_start, window_width) => {
    return (e) => {
      let touchobj = e.changedTouches[0];
      this.dist = parseInt(touchobj.clientX) - this.startx;
      document.getElementById("job-"+id+index).style.left =
        (job_start + (this.dist*100 / window_width))+"%";

      let end_dates = [...this.props.all_jobs_data[id].end_dates];
      let start_dates = [...this.props.all_jobs_data[id].start_dates];

      let hour = end_dates[index].getHours() + (end_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24);
      let start_hour = start_dates[index].getHours() + (start_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24);

      const min = Math.round((hour % 1) * 60);
      let hr = Math.floor(hour);
      let ampm = "am";
      if( hr > 12 ) {
          hr -= 12;
          ampm = "pm";
      }

      const s_min = Math.round((start_hour % 1) * 60);
      let s_hr = Math.floor(start_hour);
      let s_ampm = "am";
      if( hr > 12 ) {
          hr -= 12;
          ampm = "pm";
      }

      document.getElementById("job-end-tooltip-"+id+index).innerHTML = hr+":"+(min<10?"0"+min:min)+" "+ampm;
      document.getElementById("job-start-tooltip-"+id+index).innerHTML = s_hr+":"+(s_min<10?"0"+s_min:s_min)+" "+s_ampm;

      e.stopPropagation();
      e.preventDefault();
    }
  }

  handleBodyTouchEnd = (id, index, job_start, window_width) => {
    return (e) => {
      let end_dates = [...this.props.all_jobs_data[id].end_dates];
      let start_dates = [...this.props.all_jobs_data[id].start_dates];

      let hour = end_dates[index].getHours() + (end_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24);
      let start_hour = start_dates[index].getHours() + (start_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24);

      let value = new Date(new Date(end_dates[index]).setHours(Math.floor(hour), (hour % 1) * 60));
      let start_value = new Date(new Date(start_dates[index]).setHours(Math.floor(start_hour), (start_hour % 1) * 60));

      end_dates[index] = value;
      start_dates[index] = start_value;
      this.props.editJob(id, {end_dates:end_dates, start_dates:start_dates});

      e.currentTarget.removeEventListener('touchend', this.event_store["BodyTouchEnd"+id+index], false);
      e.currentTarget.removeEventListener('touchmove', this.event_store["BodyTouchMove"+id+index], false);
      e.currentTarget.removeEventListener('touchcancel', this.event_store["BodyTouchEnd"+id+index], false);
      e.stopPropagation();
      e.preventDefault();
    }
  }


  handleBodyMouseDown = (id, index, job_start) => {
    return (e) => {
      this.dragged_element = {
        id,
        index,
        job_start,
        side:"end",
        window_width: e.currentTarget.parentNode.parentNode.clientWidth,
        e
      }
      this.dist = 0;
      this.startx = parseInt(e.clientX);
      document.addEventListener('mousemove', this.handleBodyMouseMove, false);
      document.addEventListener('mouseup', this.handleBodyMouseUp, false);
      e.preventDefault();
      e.stopPropagation();
    }
  }

  handleBodyMouseMove = (e) => {
    const {id, index, window_width, job_start} = this.dragged_element;
    this.dist = parseInt(e.clientX) - this.startx;

    document.getElementById("job-"+id+index).style.left =
      (job_start + (this.dist*100 / window_width))+"%";

    let end_dates = [...this.props.all_jobs_data[id].end_dates];
    let start_dates = [...this.props.all_jobs_data[id].start_dates];

    let hour = end_dates[index].getHours() + (end_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24);
    let start_hour = start_dates[index].getHours() + (start_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24);

    const min = Math.round((hour % 1) * 60);
    let hr = Math.floor(hour);
    let ampm = "am";
    if( hr > 12 ) {
        hr -= 12;
        ampm = "pm";
    }

    const s_min = Math.round((start_hour % 1) * 60);
    let s_hr = Math.floor(start_hour);
    let s_ampm = "am";
    if( hr > 12 ) {
        hr -= 12;
        ampm = "pm";
    }

    document.getElementById("job-end-tooltip-"+id+index).innerHTML = hr+":"+(min<10?"0"+min:min)+" "+ampm;
    document.getElementById("job-start-tooltip-"+id+index).innerHTML = s_hr+":"+(s_min<10?"0"+s_min:s_min)+" "+s_ampm;

    e.stopPropagation();
    e.preventDefault();
  }

  handleBodyMouseUp = (e) => {
    const {id, index, window_width} = this.dragged_element;
    let end_dates = [...this.props.all_jobs_data[id].end_dates];
    let start_dates = [...this.props.all_jobs_data[id].start_dates];

    let hour = end_dates[index].getHours() + (end_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24);
    let start_hour = start_dates[index].getHours() + (start_dates[index].getMinutes() / 60) + ((this.dist / window_width) * 24);

    let value = new Date(new Date(end_dates[index]).setHours(Math.floor(hour), (hour % 1) * 60));
    let start_value = new Date(new Date(start_dates[index]).setHours(Math.floor(start_hour), (start_hour % 1) * 60));

    end_dates[index] = value;
    start_dates[index] = start_value;
    this.props.editJob(id, {end_dates:end_dates, start_dates:start_dates});

    document.removeEventListener('mousemove', this.handleBodyMouseMove, false);
    document.removeEventListener('mouseup', this.handleBodyMouseUp, false);

    e.stopPropagation();
    e.preventDefault();
  }

  render() {
    const cells = [];
    const day_cells = [];
    const classes = [
      "jobs-0",
      "jobs-1",
      "jobs-2",
      "jobs-3",
      "jobs-4",
      "jobs-5",
      "jobs-6",
      "jobs-7",
      "jobs-8"
    ];

    var jobs_by_day = this.calcJobsByDay();

    if (!this.props.selected_day && jobs_by_day && jobs_by_day.length === DaysInMonth(this.props.month, this.props.year)) {
      for (var i=0; i<DaysInMonth(this.props.month, this.props.year); i++) {

        const current_date = new Date(this.props.year, this.props.month, (i+1));
        let value = 0;
        for (var t=0; t<jobs_by_day[i].length; t++) {
          const job = jobs_by_day[i][t];
          const job_duration = (((job.end_date-job.start_date)/1000)/60/60);
          value+=job_duration;
        }
        let weekend = "";
        // value = Math.max(value,0);
        for (var w=0; w<this.props.company_settings.weekend.length; w++) {
          if (current_date.getDay() === this.props.company_settings.weekend[w]) {
            // value = 0;
            weekend = " weekend";
          }
        }
        cells.push((
          <li
            key={this.props.user_id + "_" + i}
            className={"cell " +
              (classes[Math.min(Math.round(value*8/this.props.company_settings.daily_work_hours),classes.length-1)]) +
              weekend +
              (((current_date.toDateString() === (new Date()).toDateString()) || (((this.props.month/20)+this.props.year) > (((new Date()).getMonth()/20)+(new Date()).getFullYear()) && i===0)) ? " today" : "") +
              this.isDayAssigned(current_date)
          } onClick={this.handleDayClick(i+1, this.props.index)}
          >{
            value?(Math.round(value*10)/10)+" hrs":""
          }</li>
        ));
      }
    }

    if (this.props.selected_day) {
      // The Day view

      for (var h=0; h<24; h++) {
        // const current_time = new Date(this.props.year, this.props.month, this.props.selected_day, h);
        day_cells.push((
          <li
            key={"hour-"+h}
            className={
              "day-cell"
            }
            onClick={this.handleDayCellClick(h)}
          >
          </li>
        ));
      }
      if (jobs_by_day[this.props.selected_day-1]) {
        //Represent Jobs on Day View
        for (t=0; t<jobs_by_day[this.props.selected_day-1].length; t++) {
          const job = jobs_by_day[this.props.selected_day-1][t];
          const job_duration = (((job.end_date-job.start_date)/1000)/60/60/24)*100;
          const job_start = ((job.start_date - new Date(this.props.year, this.props.month, this.props.selected_day))/1000/60/60/24)*100;
          // if (this.props.all_users_data[this.props.user_id].trade_name !== "Customer" || job.project_id === this.props.selected_job_data.project_id) {
            day_cells.push((
              <li
                key={"job-"+job.id+job.index}
                id={"job-"+job.id+job.index}
                className={
                  "job" +
                  (job.id === this.props.selected_job? " selected" : "") +
                  ((job.project_id === this.props.selected_project || this.props.selected_job || !this.props.selected_project)? " in-project" : "")
                }
                style={{
                  left: job_start+"%",
                  maxWidth: job_duration+"%",
                  minWidth: job_duration+"%",
                  top: (t*4) + "px",
                  backgroundColor: job.project_color
                }}
              >
                <label>
                  <DatePicker
                    selected={new Date(job.start_date)}
                    className=""
                    onChange={this.handleChangeStartTime(job.id,job.index)}
                    dateFormat="h:mm aa"
                    dateCaption="change start date"
                    popperPlacement="left"
                    popperModifiers={{
                      offset: {
                        enabled: true,
                        offset: '0, 10px'
                      },
                      preventOverflow: {
                        enabled: true,
                        escapeWithReference: false, // force popper to stay in viewport (even when input is scrolled out of view)
                        boundariesElement: 'scrollParent'
                      }
                    }}
                    calendarContainer={({className, children})=>(
                      <div>
                        <OriginalContainer className={className}>
                          <div className="date-picker-header">change job date</div>
                          <div style={{ position: "relative" }}>{children}</div>
                        </OriginalContainer>
                      </div>
                    )}
                  />
                  <div id={"job-start-tooltip-"+job.id+job.index} className="tooltips"></div>
                  <i className="fas fa-calendar-alt"></i>
                </label>
                <div
                  onClick={
                    this.handleJobClick(job.project_id, job.id, job.index)
                  }
                  onTouchStart={this.handleBodyTouchStart(job.id, job.index, job_start)}
                  onMouseDown={this.handleBodyMouseDown(job.id, job.index, job_start)}
                >
                  <h4>
                    <i className={job.icon}></i>
                    <span><strong>{job.name}</strong></span>
                  </h4>
                  <div className="name">{job.project_name}</div>
                </div>
                <label
                  onTouchStart={this.handleEndTouchStart(job.id, job.index, job_duration)}
                  onMouseDown={this.handleEndMouseDown(job.id, job.index, job_duration)}
                >
                  <div id={"job-end-tooltip-"+job.id+job.index} className="tooltips"></div>
                  <i className="fas fa-arrows-alt-h"></i>
                </label>
              </li>
            ));
          // }
        }
      }
    }

    return (
      <div className={"user-schedule" + (this.props.selected_day ? " show-day" : "")}>
      { this.props.selected_day ? (
        <ul className={"day-schedule"}
          style={{
            // transformOrigin: ((this.props.selected_day/DaysInMonth(this.props.month, this.props.year))*100) + "% 0"
            // transition: "opacity .5s ease, transform 0s ease 1s"
          }}>
          {day_cells}
        </ul>
      ):(
        <ul className={"month-schedule"}>
          {cells}
        </ul>
      )}
      </div>
    );
  }
}

