export function GenerateTasks (name, scheduled, progress, job_id) {
  let tasks = [];
  let task_id = "";
  if (JobDefaultTasks[name]) {
    for (let t=0; t<JobDefaultTasks[name].length; t++) {
      task_id = "task_" + GenerateCode();
      TasksById[task_id] = {
        name: JobDefaultTasks[name][t],
        description: '<p>' + JobDefaultTasks[name][t] + ' is a cool task.</p>',
        completed: scheduled && progress ? !!(Math.round(Math.random()*100)%5) : false,
        id: task_id,
        job_id: job_id
      };
      tasks.push(task_id);
    }
  }
  return tasks;
}

let job_iterator = 0;
export function GenerateJob (double, project, name, date, scheduled, progress) {
  job_iterator++;
  var start_time = 6+(Math.random()*DAILY_WORK_HOURS/4);
  var end_time = Math.max(1,start_time+Math.random()*DAILY_WORK_HOURS);
  var id = "t"+job_iterator+"p"+project.id;
  //var scheduled = Math.round(Math.random()*100) % 4;
  let default_trade = JobDefaultTrades[name];
  let user;
  if (scheduled && default_trade && UsersByTrade[default_trade]) {
    if (default_trade === "Customer") {
      for (var u=0; u<UsersByTrade[default_trade].length; u++) {
        if (project.id === UsersByTrade[default_trade][u].visible_projects[0]) {
          user = UsersByTrade[default_trade][u];
        }
      }
      if (!user) {
        scheduled = false;
      }
    } else {
      user = UsersByTrade[default_trade][Math.floor(Math.random()*UsersByTrade[default_trade].length)];
    }
  } else {
    scheduled = false;
  }

  let tasks = GenerateTasks(name, scheduled, progress, id);

  progress = 0;
  for (let t=0; t<tasks.length; t++) {
    if (tasks[t].completed) {
      progress++;
    }
  }

  var job = {
    start_dates: double ?
      [
          new Date(date.getFullYear(), date.getMonth(), date.getDate(), start_time ),
          new Date(date.getFullYear(), date.getMonth(), date.getDate()+1, start_time ),
          new Date(date.getFullYear(), date.getMonth(), date.getDate()+2, start_time )
      ] : scheduled ?
        [new Date(date.getFullYear(), date.getMonth(), date.getDate(), start_time )]
      : [],
    end_dates: double ?
      [
          new Date(date.getFullYear(), date.getMonth(), date.getDate(), end_time ),
          new Date(date.getFullYear(), date.getMonth(), date.getDate()+1, end_time ),
          new Date(date.getFullYear(), date.getMonth(), date.getDate()+2, end_time )
      ] : scheduled ? [new Date(date.getFullYear(), date.getMonth(), date.getDate(), end_time )] : [],
    assigned_user: scheduled ? user.id : null,
    project_id: project.id,
    project_color: project.color,
    project_name: project.name,
    default_trade: default_trade,
    name: name,
    icon: JobIcons[name] || DefaultJobIcon,
    progress: scheduled ? Math.round(100*(progress / tasks.length)) || 0 : 0,//scheduled ? Math.min(Math.round(Math.random()*200),100) : 0,
    id: id,
    tasks: tasks
  };
  if (user) {
    user.jobs.push(job.id);
  }

  return job;
}

