export function CreateCustomerUser (project) {
  var id =  Math.random();
  var name = random_names[Math.floor(Math.random()*random_names.length)].split(" ");
  const user = {
    "jobs":[],
    "tasks":[],
    "id": id,
    "first_name":name[0],
  	"last_name": project.name.substr(0,project.name.indexOf(' ')),
  	"trade_name": "Customer",
    "address": "1234 N 360 St. City, State, 55555",
    "billing_address": "1234 N 360 St. City, State, 55555",
    visible_projects: [project.id]
  };

  UsersByTrade["Customer"].push(user);
  // Users.push(user);
  UsersById[id] = user;
  return user;
}

export function GenerateProjectJobs () {

  for (var u=0; u<Users.length; u++) {
    UsersById[Users[u].id] = Users[u];
  }

  for (u=0; u<random_names.length; u++) {
    var id = "user"+Math.random();
    var names = random_names[u].split(" ");
    var trade_name = Trades[TradeDistributor[Math.floor(Math.random()*TradeDistributor.length)]];
    UsersById[id] = {
      "jobs":[],
      "tasks":[],
      "roles":trade_name==="Project Manager"?["project-manager"]:[],
      "id": id,
      "first_name": names[0],
    	"last_name": names[1],
      "email": names[0] + "." + names[1] + "@construction.com",
      "photo_uri": "https://randomuser.me/api/portraits/"+ (names[2]==="m" ? "men" : "women") +"/"+ Math.ceil(Math.random()*80+12) +".jpg",
    	"trade_name": trade_name
    };
  }


  const today = new Date();
  let weekend_ref = {};
  for (let w=0; w<WEEKEND.length; w++) {
    weekend_ref[WEEKEND[w]] = true;
  }
  for (let u in UsersById) {
    const user = UsersById[u];
    UsersByTrade[user.trade_name] = UsersByTrade[user.trade_name] || [];
    UsersByTrade[user.trade_name].push(user);
  }
  for (let p=0; p<ProjectData.length; p++) {
    let project = ProjectData[p];
    let start_date = new Date(today.getFullYear(), today.getMonth(), today.getDate()-(p*2));//Math.round(project.temp_progress*2));
    let weekend_adjuster = 0;

    var customer = CreateCustomerUser(project);

    project.customer_id = customer.id;
    project.address = customer.address;

    project.blob_uri = "./blob/photos/";
    project.photos = [];
    for (let ph=0; ph<photos.length; ph++) {
      let photo = {
        src: project.blob_uri + photos[ph].p,
        width: photos[ph].w,
        height: photos[ph].h,
        name: "A job well done.",
        description: "We need some more work like this around here!"
      };
      photo.id = "photo_"+Math.random();
      PhotosById[photo.id] = photo;
      project.photos.push(photo.id);
    }
    let temp_jobs = ProjectTemplates[project.temp_jobs].jobs;
    for (let t=0; t<temp_jobs.length; t++) {

      let date = new Date(start_date.getFullYear(), start_date.getMonth(), start_date.getDate()+t+weekend_adjuster);
      let progress = t < p ? 100 :
                      t === p ? Math.min(Math.round(Math.random()*100),100) : 0;
      while (weekend_ref[date.getDay()]) {
        weekend_adjuster++;
        date = new Date(start_date.getFullYear(), start_date.getMonth(), start_date.getDate()+t+weekend_adjuster);
      }
      const job = GenerateJob(
        0,
        project,
        JobTemplates[temp_jobs[t]].name,
        date,
        (progress === 0 ? !!Math.round(Math.random()*100%4) : true),
        progress
      );
      project.jobs.push(job.id);
      if (!JobsByMonth[date.getFullYear() + "_" + date.getMonth()]) {
        JobsByMonth[date.getFullYear() + "_" + date.getMonth()] = [];
      }
      JobsByMonth[date.getFullYear() + "_" + date.getMonth()].push(job);
      JobsById[job.id] = job;
    }
  }
}


