import React from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {
  selectJob,
  updateJobsData,
  updateUsersData,
  editJob,
  openJobCreator,
  closeJobCreator,
  openProjectEditor,
  openProjectSummary,
  openPhotoGallery,
  openInvoiceEditor
} from '../actions/actions';

import {Content} from '../utilities/content'
import {JobsById, UsersById} from '../utilities/data'

const MonthNames = Content[window.loc].MonthNames;



class Project extends React.Component {
  state = {
    loaded: false,
    selected_job: "none"
  };

  getJobData () {
    var jobs_by_id = {};
    let the_job_id;
    this.props.project_data.jobs.map((job_id, i) => {
      the_job_id = job_id;
      let job_insert = {
        ...JobsById[job_id]
      };
      jobs_by_id[job_insert.id] = job_insert;
      return true;
    });
    if (!this.props.all_jobs_data[the_job_id]) {
      this.props.updateJobsData(jobs_by_id);
    }
    if (!this.props.all_users_data[this.props.project_data.customer_id]) {
      this.props.updateUsersData({[this.props.project_data.customer_id]: UsersById[this.props.project_data.customer_id]})
    }
    if (!this.props.project_data.jobs.length) {
      this.props.openJobCreator(this.props.project_data.id);
    }
  }

  componentDidMount() {
    this.getJobData();
    // this.setState({
    //   months: data.months
    // });
  }

  componentDidUpdate(prevProps) {
    if (this.props.project_data.id !== prevProps.project_data.id) {
      this.getJobData();
      // this.setState({
      //   months: data.months
      // });
    }
  }

  handleAddButton() {
    this.props.openJobCreator(this.props.project_data.id);
  }


  selectJob = (id, index) => {
    return (e) => {
      e.stopPropagation();
      this.props.selectJob(this.props.id, id, index);
      this.props.closeJobCreator();
    }
  }

  handleCloseButton = (e) => {
    this.props.selectJob(null, null);
  }

  handleInvoiceButton = (e) => {
    e.preventDefault();
    e.stopPropagation();
    let invoice_id = this.props.all_projects_data[this.props.project_data.id].invoice_id || "NEWINVOICE";

    console.log(this.props.all_projects_data[this.props.project_data.id]);

    this.props.openInvoiceEditor({
      invoice_id: invoice_id,
      project_id: this.props.project_data.id
    });
  }

  handleEditButton = (e) => {
    e.preventDefault();
    e.stopPropagation();
    this.props.openProjectEditor(this.props.project_data.id);
  }

  handleSummaryButton = (e) => {
    e.preventDefault();
    e.stopPropagation();
    this.props.openProjectSummary(this.props.project_data.id);
  }

  handlePhotoButton = (e) => {
    e.preventDefault();
    e.stopPropagation();
    this.props.openPhotoGallery(this.props.project_data.id);
  }

  render() {
    var months = {
      Unscheduled: []
    };
    var display_months = [];
    var today = new Date();

    if (this.props.all_jobs_data && this.props.project_data.jobs) {
      this.props.project_data.jobs.map((job_id, i) => {
        let job_insert = {
          ...this.props.all_jobs_data[job_id]
        };
        if (!job_insert.id) {
          return true;
        }
        if (!job_insert.start_dates.length) {
          months.Unscheduled.push(job_insert);
          return true;
        }

        job_insert.start_dates.map((start_date, i) => {
          let job_month = start_date.getMonth();
          if (!months[job_month]) {
            months[job_month] = [];
          }
          months[job_month].push({...job_insert, start_date: start_date, end_date: job_insert.end_dates[i], index: i});
          return true;
        });
        return true;
      });
      var month_keys = Object.keys(months);
      var after_today = false;
      month_keys.sort(function(a, b){
        return (months[a][0]?months[a][0].start_date:0) - (months[b][0]?months[b][0].start_date:0)
      });
      for (var i=0; i<month_keys.length; i++) {
        var jobs = months[month_keys[i]];
        jobs.sort(function(a, b){return a.start_date - b.start_date});
        for (var j=0; j<jobs.length; j++) {
          var job = jobs[j];
          var mark_today = "";
          if (!after_today && job.start_date && job.start_date >= new Date(today.getFullYear(), today.getMonth(), today.getDate())) {
            after_today = true;
            mark_today = " after-today";
          }
          if (!job.id) {
            console.log("no id", job);
          }
          display_months.push((
            <li
              key={job.id + (job.index||"")}
              data-id={job.id}
              className={
                (job.progress === 100 ? "complete" : job.progress > 0 ? "progress" : "") +
                (" "+month_keys[i]) +
                (job.start_date && (job.start_date.toDateString() === (new Date()).toDateString())?" today":"") +
                mark_today +
                (j === jobs.length-1?" last-of-month":"") +
                (job.id===this.props.selected_job?" selected":" not-selected") +
                ((job.id===this.props.selected_job && job.index===this.props.selected_job_index)?" indicated":"") +
                ((j<jobs.length-1) && jobs[j+1].id===this.props.selected_job?" job-before-selected":"")
              }
              onClick={this.selectJob(job.id, job.index)}
            >
              {
                j===0?<h3>{MonthNames[month_keys[i]] || month_keys[i]}</h3>:[]
              }
              <span className="h-line" />
              <span className="selected-arrow" />
              <span className="date">{(job.start_date ? job.start_date.getDate() : "")}</span>
              <div className="job-box">
                <h4><i className={job.icon}></i><span className="job-name">{job.name}</span></h4>
              </div>
              <span className="status">
                <span className="progress-bar" style={{transform:"scaleX("+((job.progress||0)/100)+")"}}></span>
                <span className="status-text">{
                  job.progress===100 ?
                  (<i className="fas fa-check"></i>):
                  !job.progress ?
                  (""):
                  (job.progress + "%")
                }</span>
              </span>
            </li>
          ));
        };
      };
    }
    return (
      <article
        className={"project" +
          (this.props.last_selected?" last_selected":"")
        }
      >
        <header>
          <h2>{this.props.project_data.name}</h2>
          <div className="button-menu">
            <button title="create invoice" onClick={this.handleInvoiceButton} className="icon-button"><i className="fas fa-file-invoice-dollar"></i></button>
            <button title="edit details" onClick={this.handleEditButton} className="icon-button"><i className="fas fa-edit"></i></button>
            <button title="photo gallery" onClick={this.handlePhotoButton} className="icon-button"><i className="fas fa-images"></i></button>
            <button title="project summary" onClick={this.handleSummaryButton} className="icon-button"><i className="fas fa-info"></i></button>

            {this.props.selected_project === this.props.project_data.id && (<button title="close" onClick={this.handleCloseButton} className="icon-button"><i className="fas fa-times"></i></button>)}
          </div>
        </header>
        <div className="header-color" style={{
          backgroundColor: this.props.project_data.color || "black"
        }} />
        <div className="timeline">
          {display_months}
          <div className="add-job-button">
            <button
              onClick={()=>{
                this.handleAddButton();
              }}
              className="icon-button"
            >
              <i className="fas fa-plus"></i>
            </button>
          </div>
        </div>
      </article>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    all_projects_data: state.state.all_projects_data,
    all_users_data: state.state.all_users_data,
    all_jobs_data: state.state.all_jobs_data,
    selected_job: state.state.selected_job,
    selected_job_index: state.state.selected_job_index,
    selected_project: state.state.selected_project
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    selectJob,
    updateJobsData,
    updateUsersData,
    editJob,
    openJobCreator,
    closeJobCreator,
    openProjectEditor,
    openProjectSummary,
    openPhotoGallery,
    openInvoiceEditor
  }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Project);
