import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { signOutUser, signInUser, updateRoles } from '../actions/actions';

//import
import {
  Route,
  Switch,
  Redirect
} from 'react-router-dom'

import DragDropContainer from './drag-drop-container';


import Header from './header';
import Footer from './footer';
import {URLString} from '../utilities/string';

import PageSignIn from './page-signin';
import PageSetup from './page-setup';
import PageDashboard from './page-dashboard';
import PageSchedule from './page-schedule';
import PageLeads from './page-leads';
import PageProjects from './page-projects';
import PageAdmin from './page-admin';
import Page404 from './page-404';


import {DefaultUser, Roles} from '../utilities/data';

// import API from '../utilities/api';
// import Axios from 'axios';

import {HOMEPAGE, applicationConfig} from '../config/config';


import * as Msal from 'msal';
// let Msal = window.Msal;


class App extends Component {
  constructor (props) {
    const authCallback = (errorDesc, token, error, tokenType) => {
        //This function is called after loginRedirect and acquireTokenRedirect. Not called with loginPopup
        // msal object is bound to the window object after the constructor is called.
      if (token) {
        this.props.signInUser(DefaultUser);
        console.log(token);
      }
      else {
        console.log(error + ":" + errorDesc);
      }
    }

    window.clientApplication = new Msal.UserAgentApplication(
      applicationConfig.clientID,
      applicationConfig.authority,
      authCallback,
      {
        redirectUri: 'http://localhost:3000/',
        cacheLocation: 'localStorage',
        validateAuthority: false
      }
    );

    props.updateRoles(Roles);

    // window.clientApplication.cacheLocation = 'localStorage';

    super(props);
    this.state = {

    }
  }
  componentDidMount() {
    window.openModal = (modal_object) => {
      this.setState({
        modal: modal_object
      });
      setTimeout(()=>{
        this.setState({modal_state: "show"});
      }, 50);
    }
    window.closeModal = () => {
      this.setState({modal_state: ""});
      setTimeout(()=>{
        this.setState({
          modal: null
        });
      }, 750);
    }
    // Create a new style element
    const el = document.createElement('style');
    el.type = 'text/css';

    // Add it to the head of the document
    const head = document.querySelector('head');
    head.appendChild(el);

    // At some future point we can totally redefine the entire content of the style element
    window.setStyle = (newStyles) => {
      el.innerHTML = newStyles;
    };

    window.printDiv = (divId) => {
      let printDivCSS = '<link href="./printstyle.css" rel="stylesheet" type="text/css"><link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.4.2/css/all.css" integrity="sha384-/rXc/GQVaYpyDdyxK+ecHPVYJSN9bmVFBvjA/9eOB+pb3F2w2N6fc5qB9Ew5yIns" crossorigin="anonymous"><div class="document">';

      window.frames["print_frame"].document.body.innerHTML=printDivCSS + document.getElementById(divId).innerHTML + "</div>";
      window.frames["print_frame"].window.focus();

      setTimeout(()=>{
        window.frames["print_frame"].window.print();
      }, 1000);
    }

  }

  componentDidUpdate(prevProps) {
    if (this.props.selected_project !== prevProps.selected_project) {
      // this.props.history.push("/projects" + (this.props.selected_project ? "/" + URLString(this.props.all_projects_data[this.props.selected_project].id) : ""));
      this.props.history.push("/projects" + (this.props.selected_project ? "/" + URLString(this.props.selected_project) : ""));
    }
  }

  render() {
    const {current_user} = this.props;
    const homepage = current_user ? current_user.config.homepage || current_user.company_config.homepage || HOMEPAGE : HOMEPAGE;
    let redirect;

    if (localStorage.getItem("redirect")) {
      redirect = localStorage.getItem("redirect");
      // localStorage.removeItem("redirect");
    }

    if (window.location.pathname !== "/") {
      localStorage.setItem("redirect", window.location.pathname);
    }

    return (
      <div id="app">
        { this.state.modal && (
          <div id="modal-container" className={this.state.modal_state}>{this.state.modal}</div>
        )}
        <input id="nav-menu-checkbox" type="checkbox" />

        <Route component={Header} />
        <main>
          <DragDropContainer>
            <Switch>
              <Route exact path="/ah-geez-404" component={Page404} key="404" />

              { current_user ? (
                !current_user.company_setup || !current_user.user_setup ? [
                  <Route exact path="/setup" component={PageSetup} key="setup" />,
                  <Redirect to="/setup" key="to-setup" />
                ]:[
                  <Redirect exact from="/signin" to={homepage} key="to-home-from-signin" />,
                  <Redirect exact from="/" to={redirect || homepage} key="to-home" />,
                  <Route exact path="/dashboard" component={PageDashboard} key="dashboard" />,
                  <Route path="/projects" component={PageProjects} key="projects" />,
                  <Route exact path="/schedule" component={PageSchedule} key="schedule" />,
                  <Route path="/leads" component={PageLeads} key="leads" />,
                  (current_user.roles.indexOf("admin")+1) && <Route path="/admin" component={PageAdmin} key="admin" />
                ]
              ):[
                // !code ? [
                //   <Route component={PageSignIn} key="sign-in" />
                // ]:[
                  <Route exact path="/" component={PageSignIn} key="sign-in" />,
                  <Redirect to="/" key="to-home" />
                // ]
              ]}
              <Route component={Page404} />
              <Redirect to="/ah-geez-404" key="to-404" />
            </Switch>
          </DragDropContainer>
        </main>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    current_user: state.state.current_user,
    selected_project: state.state.selected_project
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({signOutUser, signInUser, updateRoles}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
