import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {updateCompanySettings} from '../actions/actions';

import {Content} from "../utilities/content";

const WeekDaysShort = Content[window.loc].WeekDaysShort;

class UserEditor extends React.Component {

  handleChangeSetting = (setting, value) => {
    this.props.updateCompanySettings({
      ...this.props.company_settings,
      [setting]: value
    });
  }

  onRoleChange = (day) => {
    return (e) => {
      let days = [...(this.props.company_settings.weekend || [])];
      if (!e.target.checked) {
        days.push(day);
      } else {
        days.splice(days.indexOf(day),1);
      }
      this.handleChangeSetting("weekend", days)
    }
  }

  handleWorkHours = (e) => {
    let value = parseInt(e.target.value, 10);
    if (value>24) {
      value = 24;
    }
    else if (value<1) {
      value = 1;
    }
    this.handleChangeSetting("daily_work_hours", value)
  }

  render() {
    let weekend_nodes = [];

    for (var r=0; r<WeekDaysShort.length; r++) {
      var day = WeekDaysShort[r];

      weekend_nodes.push((
        <label className="checkbox-container" key={day}>
          <input
            type="checkbox"
            onChange={this.onRoleChange(r)}
            checked={!(this.props.company_settings.weekend.indexOf(r)+1)}
          />
          <i className="fas fa-check-square"></i>
          <i className="far fa-square"></i>
          <span>{day}</span>
        </label>
      ));
    }


    return (
      <div className={"advanced-settings"}>

        <div className="week-days">
          <h3>work days</h3>
          {weekend_nodes}
        </div>

        <div className="daily-hours">
          <h3>daily work hours</h3>
          <input
            value={this.props.company_settings.daily_work_hours}
            onChange={this.handleWorkHours}
            type="number" max="24" min="1"
          />
        </div>

      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    company_settings: state.state.company_settings
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({updateCompanySettings}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(UserEditor);
