import DefaultState from '../store/default-state'

function assignJobToUser (state, action) {
  let old_user = {};

  if (action.old_user_id) {
    old_user = {...state.all_users_data[action.old_user_id]};
    var index = old_user.jobs.indexOf(action.job_id);
    if (index > -1) {
      old_user.jobs.splice(index, 1);
    }
  }

  let user = {...state.all_users_data[action.user_id]};
  let needs_job = true;
  for (let i=0; i<user.jobs.length; i++) {
    if (user.jobs[i] === action.job_id) {
      needs_job = false;
      break;
    }
  }
  if (needs_job) {
    user.jobs.push(action.job_id);
  }
  if (action.old_user_id) {
    return {
      ...state,
      all_users_data: {
        ...state.all_users_data,
        [action.user_id]:user,
        [action.old_user_id||"no-old-user"]:old_user
      }
    }
  }
  return {
    ...state,
    all_users_data: {
      ...state.all_users_data,
      [action.user_id]:user
    }
  }
}

function openJobCreator (state, action) {
  return {
    ...state,
    open_job_creator: {project_id: action.project_id, job_id: action.job_id}
  }
}

function closeJobCreator (state, action) {
  return {
    ...state,
    open_job_creator: null
  }
}

function addJob (state, action) {
  let project_data = {...state.all_projects_data};
  let lead_data = {...state.all_leads_data};

  if (project_data[action.project_id]) {
    // add to project
    if (action.project_id) {
      project_data[action.project_id].jobs.push(action.job_id);
    }
  }
  else {
    // add to lead
    if (action.lead_id) {
      lead_data[action.lead_id].jobs.push(action.job_id);
    }
  }

  return {
    ...state,
    all_projects_data: project_data,
    all_leads_data: lead_data,
    all_jobs_data: {
      ...state.all_jobs_data,
      [action.job_id]:action.job_object
    }
  }
}

function deleteJob (state, action) {
  let all_jobs_data = {...state.all_jobs_data};
  delete all_jobs_data[action.job_id];

  let jobs = [...state.all_projects_data[action.project_id].jobs];
  for (var j=0; j<jobs.length; j++) {
    if (jobs[j] === action.job_id) {
      jobs.splice(j, 1);
      break;
    }
  }

  return {
    ...state,
    all_jobs_data: {
      ...all_jobs_data
    },
    all_projects_data: {
      ...state.all_projects_data,
      [action.project_id]: {
        ...state.all_projects_data[action.project_id],
        jobs: jobs
      }
    },
    selected_job: null
  }
}

function updateProjectColor (state, action) {
  let project_data = {...state.all_projects_data[action.project_id]};
  let jobs = {};
  for (var j=0; j<project_data.jobs.length; j++) {
    let job = {...state.all_jobs_data[project_data.jobs[j]]};

    job.project_color = action.color;

    jobs[job.id] = job;
  }

  return {
    ...state,
    all_projects_data: {
      ...state.all_projects_data,
      [action.project_id]: {...state.all_projects_data[action.project_id], color: action.color}
    },
    all_jobs_data: {
      ...state.all_jobs_data,
      ...jobs
    }
  }
}









const reducers = (state = DefaultState, action) => {
  let user;
  switch (action.type) {
    case 'EXAMPLE':
      return {
        ...state,
        examplePropOne: 'newPropOne'
      }
    case 'EXAMPLE-TWO':
      return {
        ...state,
        examplePropTwo: action.sampleData + action.moreData
      }
    case 'SETUP-COMPANY':
      return {
        ...state,
        current_user: {...(state.current_user||{}), company_setup:true}
      }
    case 'SETUP-USER':
      return {
        ...state,
        current_user: {...(state.current_user||{}), user_setup:true}
      }
    case 'SIGNIN-USER':
      return {
        ...state,
        current_user: action.user_data
      }
    case 'SIGNOUT-USER':
      return {
        ...state,
        current_user: null
      }
    case 'SELECT-JOB':
      return {
        ...state,
        selected_project: action.project_id,
        selected_job: action.job_id,
        selected_job_index: action.index || 0
      }




    case 'UPDATE-ALL-USERS-DATA':
      return {
        ...state,
        all_users_data: action.all_users_data
      }
    case 'UPDATE-USERS-DATA':
      return {
        ...state,
        all_users_data: {
          ...state.all_users_data,
          ...action.users_data
        }
      }
    case 'EDIT-USER':
      return {
        ...state,
        all_users_data: {
          ...state.all_users_data,
          [action.user_id]:{
            ...state.all_users_data[action.user_id],
            ...action.user_object
          }
        }
      }
    case 'ASSIGN-JOB-TO-USER':
      return assignJobToUser(state, action);
    case 'ASSIGN-TASK-TO-USER':
      user = {...state.all_users_data[action.user_id]};
      user.tasks.push(action.job_id);
      return {
        ...state,
        all_users_data: {
          ...state.all_users_data,
          [action.user_id]:user
        }
      }
    case 'SELECT-USER':
      return {
        ...state,
        selected_user: action.user_id
      }




    case 'UPDATE-ALL-LEADS-DATA':
      return {
        ...state,
        all_leads_data: action.all_leads_data
      }
    case 'UPDATE-LEADS-DATA':
      return {
        ...state,
        all_leads_data: {
          ...state.all_leads_data,
          ...action.leads_data
        }
      }
    case 'SELECT-LEAD':
      return {
        ...state,
        selected_lead: action.lead_id
      }
    case 'OPEN-LEAD-EDITOR':
      return {
        ...state,
        open_lead_editor: action.lead_id
      }

    case 'EDIT-LEAD':
      return {
        ...state,
        all_leads_data: {
          ...state.all_leads_data,
          [action.lead_id]:{
            ...state.all_leads_data[action.lead_id],
            ...action.lead_object
          }
        }
      }



    case 'UPDATE-ALL-PROJECT-DATA':
      return {
        ...state,
        all_projects_data: action.all_projects_data
      }
    case 'UPDATE-PROJECTS-DATA':
      return {
        ...state,
        all_projects_data: {
          ...state.all_projects_data,
          ...action.projects_data
        }
      }
    case 'UPDATE-PROJECT-COLOR':
      return updateProjectColor(state, action);
    case 'OPEN-PHOTO-GALLERY':
      return {
        ...state,
        open_photos: action.project_id
      }
    case 'UPDATE-PHOTOS-DATA':
      return {
        ...state,
        all_photos_data: {
          ...state.all_photos_data,
          ...action.photos_data
        }
      }
    case 'EDIT-PHOTO':
      return {
        ...state,
        all_photos_data: {
          ...state.all_photos_data,
          [action.photo_id]:{
            ...state.all_photos_data[action.photo_id],
            ...action.photo_object
          }
        }
      }



    case 'OPEN-INVOICE-EDITOR':
      return {
        ...state,
        open_invoice_editor: action.invoice_id
      }

    case 'EDIT-INVOICE':
      return {
        ...state,
        all_invoices_data: {
          ...state.all_invoices_data,
          [action.invoice_id]:{
            ...state.all_invoices_data[action.invoice_id],
            ...action.invoice_object
          }
        }
      }

    case 'OPEN-PROJECT-EDITOR':
      return {
        ...state,
        open_project_editor: action.project_id,
        new_project_data: action.project_data
      }
    case 'OPEN-PROJECT-SUMMARY':
      return {
        ...state,
        open_project_summary: action.project_id
      }
    case 'EDIT-PROJECT':
      return {
        ...state,
        all_projects_data: {
          ...state.all_projects_data,
          [action.project_id]:{
            ...state.all_projects_data[action.project_id],
            ...action.project_object
          }
        }
      }


    case 'SELECT-JOB-SCHEDULE':
      return {
        ...state,
        selected_job_schedule: {
          date: action.date,
          user_id: action.user_id
        }
      }
    case 'UPDATE-ALL-JOB-DATA':
      return {
        ...state,
        all_jobs_data: action.all_jobs_data
      }
    case 'UPDATE-JOB-DATA':
      return {
        ...state,
        all_jobs_data: {
          ...state.all_jobs_data,
          ...action.job_data
        }
      }
    case 'EDIT-JOB':
      return {
        ...state,
        all_jobs_data: {
          ...state.all_jobs_data,
          [action.job_id]:{
            ...state.all_jobs_data[action.job_id],
            ...action.job_object
          }
        }
      }
    case 'DELETE-JOB':
      return deleteJob(state, action);

    case 'ADD-JOB':
      return addJob(state, action);


    case 'UPDATE-JOB-TEMPLATE-DATA':
      return {
        ...state,
        all_job_templates_data: {
          ...state.all_job_templates_data,
          ...action.template_data
        }
      }

    case 'UPDATE-PROJECT-TEMPLATE-DATA':
      return {
        ...state,
        all_project_templates_data: {
          ...state.all_project_templates_data,
          ...action.template_data
        }
      }

    case 'OPEN-JOB-EDITOR':
      return {
        ...state,
        open_job_editor: action.job_id
      }

    case 'OPEN-JOB-CREATOR':
      return openJobCreator(state, action);

    case 'CLOSE-JOB-CREATOR':
      return closeJobCreator(state, action);

    case 'UPDATE-ALL-TASK-DATA':
      return {
        ...state,
        all_tasks_data: action.all_tasks_data
      }
    case 'UPDATE-TASK-DATA':
      return {
        ...state,
        all_tasks_data: {
          ...state.all_tasks_data,
          ...action.task_data
        }
      }
    case 'ADD-TASK':
      let job_data = {...state.all_jobs_data};
      let project_data = {...state.all_projects_data};
      // add to job
      if (action.job_id) {
        job_data[action.job_id].tasks.push(action.task_id);
      }
      // add to project
      if (action.project_id) {
        project_data[action.project_id].tasks.push(action.task_id);
      }
      return {
        ...state,
        all_projects_data: project_data,
        all_jobs_data: job_data,
        all_tasks_data: {
          ...state.all_tasks_data,
          [action.task_id]:action.task_object
        }
      }
    case 'EDIT-TASK':
      return {
        ...state,
        all_tasks_data: {
          ...state.all_tasks_data,
          [action.task_id]:{
            ...state.all_tasks_data[action.task_id],
            ...action.task_object
          }
        }
      }
    case 'DELETE-TASKS':
      job_data = {...state.all_jobs_data};
      project_data = {...state.all_projects_data};
      let all_tasks_data = {...state.all_tasks_data};

      for (let i=0; i<action.task_ids.length; i++) {
        let task = all_tasks_data[action.task_ids[i]];
        //if the task is part of a job
        if (task.job_id) {
          for (let t=0; t<job_data[task.job_id].tasks.length; t++) {
            if (job_data[task.job_id].tasks[t] === action.task_ids[i]) {
              job_data[task.job_id].tasks.splice(t, 1);
            }
          }
        }
        //if the task is part of a project
        if (task.project_id) {
          for (let t=0; t<project_data[task.project_id].tasks.length; t++) {
            if (project_data[task.project_id].tasks[t] === action.task_ids[i]) {
              project_data[task.project_id].tasks.splice(t, 1);
            }
          }
        }
        //delete the task itself
        delete all_tasks_data[action.task_ids[i]];
      }
      return {
        ...state,
        all_jobs_data: job_data,
        all_projects_data: project_data,
        all_tasks_data: all_tasks_data
      }
    case 'COMPLETE-TASK':
      const completed_task = {
        ...state.all_tasks_data[action.task_id],
        completed: true
      }
      return {
        ...state,
        all_tasks_data: {
          ...state.all_tasks_data,
          [action.task_id]:completed_task
        }
      }
    case 'SELECT-TASK':
      return {
        ...state,
        selected_task: action.task_id
      }


    case 'UPDATE-ROLES':
      return {
        ...state,
        roles: {
          ...state.roles,
          ...action.roles
        }
      }



    case 'UPDATE-COMPANY-SETTINGS':
      return {
        ...state,
        company_settings: {
          ...state.company_settings,
          ...action.settings
        }
      }


    default: return state;
  }
}

export default reducers;
