import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import Editor from './editor';

import {openLeadEditor, updateLeadsData} from '../actions/actions';

class LeadEditor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // edit: this.props.edit || false
      lead_data: this.props.all_leads_data[this.props.open_lead_editor] || {
        id: Math.random()
      }
    };
  }
  componentDidUpdate(prevProps) {
    if (this.props.open_lead_editor) {
      if (this.props.open_lead_editor !== prevProps.open_lead_editor) {
        this.setState({
          lead_data: this.props.all_leads_data[this.props.open_lead_editor] || {
            id: Math.random()
          }
        });
      }
    }
  }

  handleCloseButton = (e) => {
    this.props.openLeadEditor(null);
    setTimeout(()=>{
      this.setState({
        lead_data: {
          id: Math.random()
        }
      });
    },500);
  }

  handleChange = (e) => {
    this.setState({
      lead_data: {...this.state.lead_data, [e.target.name]:e.target.value}
    });
  }

  handleDescriptionChange = (value) => {
    this.setState({
      task_data: {...this.state.task_data, description:value}
    });
  }

  handleSaveChanges = (e) => {
    if (this.props.open_lead_editor === "NEWLEAD") {
      let id=Math.random();
      let new_lead = {
        ...this.state.lead_data,
        id: id,
        new_date: new Date(),
        status: 'new'
      }
      this.props.updateLeadsData({
        [id]: new_lead
      });
    } else {
      this.props.updateLeadsData({
        [this.props.open_lead_editor]: this.state.lead_data
      });
    }

    setTimeout(()=>{
      this.setState({
        lead_data: {
          id: Math.random()
        }
      });
    },500);
  }

  render() {
    return (
      <div className={"lead-editor form" + (this.props.open_lead_editor && this.props.all_leads_data?" show":"")}>
        <div className="content">
          <header>
            <h1><i className="fas fa-phone"></i><span>{(this.state.lead_data.first_name || "") + " " + (this.state.lead_data.last_name || "")}</span></h1>
            <div>
              <button
                onClick={()=>{
                  this.handleSaveChanges();
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-check"></i>
                <span>save</span>
              </button>
              <button
                onClick={()=>{
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-times"></i>
                <span>cancel</span>
              </button>
            </div>
          </header>
          <main>
            <div>
              <div>
                <label>first name</label>
                <input name="first_name" type="text" placeholder="first name" value={this.state.lead_data.first_name || ""} onChange={this.handleChange} />
              </div>
              <div>
                <label>last name</label>
                <input name="last_name" type="text" placeholder="last name" value={this.state.lead_data.last_name || ""} onChange={this.handleChange} />
              </div>
              <div>
                <label>email</label>
                <input name="email" type="email" placeholder="email" value={this.state.lead_data.email || ""} onChange={this.handleChange} />
              </div>
              <div>
                <label>phone</label>
                <input name="phone" type="text" placeholder="phone" value={this.state.lead_data.phone || ""} onChange={this.handleChange} />
              </div>
            </div>
            <div>
              <div>
                <label>notes</label>
                <Editor
                  defaultValue={this.state.lead_data.description || ""}
                  onChange={this.handleDescriptionChange}
                  refresh={this.props.open_lead_editor || this.state.lead_data.id}
                />
              </div>
            </div>
            <div>
              <div>
                <label>address</label>
                <textarea name="address" placeholder="address" value={this.state.lead_data.address || ""} onChange={this.handleChange} />
              </div>
            </div>
            <div>
              <div>
                <label>company name</label>
                <input name="company_name" type="text" placeholder="company name" value={this.state.lead_data.company_name || ""} onChange={this.handleChange} />
              </div>
              <div>
                <label>website</label>
                <input name="website" type="text" placeholder="website" value={this.state.lead_data.website || ""} onChange={this.handleChange} />
              </div>
              <div>
                <label>birthday</label>
                <input name="birthday" type="text" placeholder="birthday" value={this.state.lead_data.birthday || ""} onChange={this.handleChange} />
              </div>
            </div>
          </main>
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    open_lead_editor: state.state.open_lead_editor,
    all_leads_data: state.state.all_leads_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({openLeadEditor, updateLeadsData}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(LeadEditor);
