import React from 'react';
import RichTextEditor from 'react-rte';

class Editor extends React.Component {


  state = {
    value: this.props.defaultValue ? RichTextEditor.createValueFromString(this.props.defaultValue, 'html') : RichTextEditor.createEmptyValue()
  }

  componentDidUpdate(prevProps) {
    if (this.props.defaultValue !== prevProps.defaultValue || this.props.refresh !== prevProps.refresh) {
      this.setState({
        value: this.props.defaultValue ? RichTextEditor.createValueFromString(this.props.defaultValue, 'html') : RichTextEditor.createEmptyValue()
      });
    }
  }

  onChange = (value) => {
    this.setState({value});
    if (this.props.onChange) {
      // Send the changes up to the parent component as an HTML string.
      // This is here to demonstrate using `.toString()` but in a real app it
      // would be better to avoid generating a string on each change.
      this.props.onChange(
        value.toString('html')
      );
    }
  };

  render () {
    return (
      <RichTextEditor
        className="editor"
        value={this.state.value}
        onChange={this.onChange}
      />
    );
  }
}

export default Editor
