import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import Editor from './editor';

import {RandomColors} from '../utilities/data';

import {openProjectEditor, editProject, updateProjectsData, updateProjectColor, updateUsersData} from '../actions/actions';


var Color = function Color(hexVal) { //define a Color class for the color objects
    this.hex = hexVal;
};

const constructColor = function(colorObj){
    var hex = colorObj.hex.substring(1);
    /* Get the RGB values to calculate the Hue. */
    var r = parseInt(hex.substring(0, 2), 16) / 255;
    var g = parseInt(hex.substring(2, 4), 16) / 255;
    var b = parseInt(hex.substring(4, 6), 16) / 255;

    /* Getting the Max and Min values for Chroma. */
    var max = Math.max.apply(Math, [r, g, b]);
    var min = Math.min.apply(Math, [r, g, b]);


    /* Variables for HSV value of hex color. */
    var chr = max - min;
    var hue = 0;
    var val = max;
    var sat = 0;


    if (val > 0) {
        /* Calculate Saturation only if Value isn't 0. */
        sat = chr / val;
        if (sat > 0) {
            if (r === max) {
                hue = 60 * (((g - min) - (b - min)) / chr);
                if (hue < 0) {
                    hue += 360;
                }
            } else if (g === max) {
                hue = 120 + 60 * (((b - min) - (r - min)) / chr);
            } else if (b === max) {
                hue = 240 + 60 * (((r - min) - (g - min)) / chr);
            }
        }
    }
    colorObj.chroma = chr;
    colorObj.hue = hue;
    colorObj.sat = sat;
    colorObj.val = val;
    colorObj.luma = 0.3 * r + 0.59 * g + 0.11 * b;
    colorObj.red = parseInt(hex.substring(0, 2), 16);
    colorObj.green = parseInt(hex.substring(2, 4), 16);
    colorObj.blue = parseInt(hex.substring(4, 6), 16);
    return colorObj;
};

const sortColorsByHue = function (colors) {
    return colors.sort(function (a, b) {
        return a.hue - b.hue;
    });
};

class ProjectEditor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // edit: this.props.edit || false
      project_data: this.props.all_projects_data[this.props.open_project_editor] || {
        id: Math.random()
      }
    };
  }

  componentDidUpdate(prevProps) {
    if (this.props.open_project_editor) {
      if (this.props.open_project_editor !== prevProps.open_project_editor) {
        if (this.props.open_project_editor === "NEWPROJECT") {
          let new_project = {};
          if (this.props.new_project_data) {
            let id=Math.random();
            let user_id = Math.random();
            let customer = {
              ...this.props.new_project_data,
              id: user_id,
              trade_name: "Customer",
              jobs: [],
              tasks: []
            }
            new_project = {
              address: this.props.new_project_data.address,
              customer_id: user_id,
              id: id,
              jobs: [],
              tasks: [],
              photos: [],
              completed: false
            }

            this.props.updateUsersData({[user_id]: customer});
          }
          this.setState({
            project_data: {
              ...new_project
            }
          });
        }
        else {
          this.setState({
            project_data: this.props.all_projects_data[this.props.open_project_editor] || {}
          });
        }
      }
    }
  }

  handleCloseButton = (e) => {
    this.props.openProjectEditor(null);
  }

  handleChange = (e) => {
    this.setState({
      project_data: {...this.state.project_data, [e.target.name]:e.target.value}
    });
  }

  handleColorChange = (e) => {
    this.setState({
      project_data: {...this.state.project_data, color:e.target.value}
    });
  };

  handleDescriptionChange = (value) => {
    this.setState({
      project_data: {...this.state.project_data, description:value}
    });
  }

  handleIconChange = (changeEvent) => {
    this.setState({
      icon: changeEvent.target.value
    });
    setTimeout(()=>{
      this.setState({
        project_data: {}
      });
    },500);
  };

  handleSaveChanges = (e) => {
    // if (this.props.open_project_editor === "NEWPROJECT") {
    //   let id=Math.random();
    //   let user_id = Math.random();
    //   let customer = {
    //     ...this.props.new_project_data,
    //     id: user_id
    //   }
    //   let new_project = {
    //     ...this.state.project_data,
    //     address: this.props.new_project_data.address,
    //     customer_id: user_id,
    //     id: id,
    //     icon: this.state.icon,
    //     completed: false
    //   }
    //
    //   this.props.updateProjectsData({[id]: new_project});
    //   this.props.updateUsersData({[user_id]: customer});
    if (this.props.open_project_editor === "NEWPROJECT") {
      this.props.updateProjectsData({[this.state.project_data.id]: this.state.project_data});
    } else {
      this.props.editProject(this.state.project_data.id, this.state.project_data);
      if (this.state.project_data.color !== this.props.all_projects_data[this.state.project_data.id].color) {
        this.props.updateProjectColor(this.state.project_data.id, this.state.project_data.color);
      }
    }
    setTimeout(()=>{
      this.setState({
        project_data: {}
      });
    },500);
  }

  render() {

    let hexArray = [...RandomColors];
    //
    // colors.sort(function(a, b){
    //   var x = a.toLowerCase();
    //   var y = b.toLowerCase();
    //   if (x < y) {return 1;}
    //   if (x > y) {return -1;}
    //   return 0;
    // });

    var colors = [];
    for (let h=0; h<hexArray.length; h++) {
      var color = new Color(hexArray[h]);
      constructColor(color);
      colors.push(color);
    };

    sortColorsByHue(colors);

    let color_nodes = [];

    for (let i=0; i<colors.length; i++) {
      color_nodes.push ((
        <div key={"color " + i} className="radio">
          <label>
            <input
              type="radio"
              value={colors[i].hex}
              checked={this.state.project_data.color === colors[i].hex}
              onChange={this.handleColorChange}
            />
            <i style={{backgroundColor: colors[i].hex}}></i>
          </label>
        </div>
      ));
    }

    return (
      <div className={"project-editor form" + (this.props.open_project_editor && this.props.all_projects_data?" show":"")}>
        <div className="content">
          <header>
            <h1>
              <span>{(this.state.project_data.name || "")}</span>
            </h1>
            <div>
              <button
                onClick={()=>{
                  this.handleSaveChanges();
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-check"></i>
                <span>save</span>
              </button>
              <button
                onClick={()=>{
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-times"></i>
                <span>cancel</span>
              </button>
            </div>
          </header>
          <main>
            <div>
              <div>
                <label>project name</label>
                <input name="name" type="text" placeholder="project name" value={this.state.project_data.name || ""} onChange={this.handleChange} />
              </div>
            </div>
            <div>
              <div>
                <label>color</label>
                <div className="color-picker">
                  {color_nodes}
                </div>
              </div>
            </div>
            <div>
              <div>
                <label>project address</label>
                <textarea name="address" placeholder="project address" value={this.state.project_data.address || ""} onChange={this.handleChange} />
              </div>
            </div>

            <div>
              <div>
                <label>notes</label>
                <Editor
                  defaultValue={(this.props.all_projects_data[this.props.open_project_editor] && this.props.all_projects_data[this.props.open_project_editor].description) || ""}
                  onChange={this.handleDescriptionChange}
                  refresh={this.props.open_project_editor || this.state.project_data.id}
                />
              </div>
            </div>
          </main>
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    selected_job: state.state.selected_job,
    open_project_editor: state.state.open_project_editor,
    new_project_data: state.state.new_project_data,
    all_projects_data: state.state.all_projects_data,
    all_jobs_data: state.state.all_jobs_data,
    all_tasks_data: state.state.all_tasks_data,
    all_users_data: state.state.all_users_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({openProjectEditor, editProject, updateProjectsData, updateProjectColor, updateUsersData}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(ProjectEditor);
