import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { editLead, editProject, addJob, updateJobsData, updateLeadsData, updateUsersData, updateProjectsData, selectJob } from '../actions/actions';


import {DragDropContext } from 'react-beautiful-dnd';

import {GenerateRandomColor, GenerateCode} from '../utilities/data';


const move = (source, destination, droppableSource, droppableDestination) => {
  const status_key = {
    "new_leads":"new",
    "scheduled_leads":"scheduled",
    "pending_leads":"pending",
    "estimate_leads":"estimate",
    "approved_leads":"approved",
    "rejected_leads":"rejected",
    "other_leads":""
  }
  const sourceClone = Array.from(source);
  const destClone = Array.from(destination);
  const [removed] = sourceClone.splice(droppableSource.index, 1);

  removed.status = status_key[droppableDestination.droppableId];
  removed[removed.status + "_date"] = removed[removed.status + "_date"] || new Date();

  destClone.splice(droppableDestination.index, 0, removed);

  const result = {};
  result.source = sourceClone;
  result.dest = destClone;
  result.removed = removed;

  return result;
};


const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
};

class DragDropContainer extends Component {

  onBeforeDragStart = () => {
    /*...*/
  };

  onDragStart = () => {
    /*...*/
  };
  onDragUpdate = () => {
    /*...*/
  };
  onDragEnd = (lead_groups) => (result) => {
    const { source, destination } = result;
    console.log(result);

    // dropped outside the list
    if (!destination) {
      return;
    }

    if (result.type==="TEMPLATE") {
      if (source.droppableId === "template_jobs" && destination.droppableId === "timeline") {
        if (this.props.selected_lead) {
          let jobs = [...this.props.all_leads_data[this.props.selected_lead].jobs];
          let template_job;
          let job_id;
          let job;

          if (!this.props.all_project_templates_data[result.draggableId]) {

            job_id = "job"+GenerateCode();
            jobs.splice(destination.index, 0, job_id);
            template_job = {...this.props.all_job_templates_data[result.draggableId]};
            job = {
              ...template_job,
              start_dates: [],
              end_dates: [],
              assigned_user: null,
              project_id: this.props.selected_lead,
              project_color: null,
              project_name: null,
              progress: 0,
              id: job_id,
              tasks: template_job.tasks || []
            };
            this.props.addJob(job_id, job, this.props.selected_lead);

          } else {
            let template_jobs = this.props.all_project_templates_data[result.draggableId].jobs;
            for (var j=template_jobs.length-1; j>=0; j--) {
              job_id = "job"+GenerateCode();
              jobs.splice(destination.index, 0, job_id);
              template_job = {...this.props.all_job_templates_data[template_jobs[j]]};
              job = {
                ...template_job,
                start_dates: [],
                end_dates: [],
                assigned_user: null,
                project_id: this.props.selected_lead,
                project_color: null,
                project_name: null,
                progress: 0,
                id: job_id,
                tasks: template_job.tasks || []
              };
              this.props.addJob(job_id, job, this.props.selected_lead);
            }
          }

          this.props.editLead(this.props.selected_lead, {jobs:jobs});
        }
      } else if (source.droppableId === "timeline" && destination.droppableId === "timeline") {
        let jobs = [...this.props.all_leads_data[this.props.selected_lead].jobs];
        const job = {...jobs.splice(source.index, 1)[0]};

        jobs.splice(destination.index, 0, job);

        console.log(job);

        this.props.editLead(this.props.selected_lead, {jobs:jobs});
      }
    }
    else if (result.type==="JOB") {
      if (this.props.selected_project) {

        let template_job;
        let job_id;
        let job;

        if (!this.props.all_project_templates_data[result.draggableId]) {
          template_job = {...this.props.all_job_templates_data[result.draggableId]};
          job_id = "job"+GenerateCode();
          job = {
            start_dates: [],
            end_dates: [],
            assigned_user: null,
            project_id: this.props.selected_project,
            project_color: this.props.all_projects_data[this.props.selected_project].color,
            project_name: this.props.all_projects_data[this.props.selected_project].name,
            default_trade: template_job.default_trade,
            name: template_job.name,
            description: template_job.description,
            icon: template_job.icon,
            progress: 0,
            id: job_id,
            tasks: template_job.tasks || []
          };
          this.props.addJob(job_id, job, this.props.selected_project);
        }
        else {
          let template_jobs = this.props.all_project_templates_data[result.draggableId].jobs;

          for (let j=0; j<template_jobs.length; j++) {
            template_job = this.props.all_job_templates_data[template_jobs[j]];
            job_id = "job"+GenerateCode();

            job = {
              start_dates: [],
              end_dates: [],
              assigned_user: null,
              project_id: this.props.selected_project,
              project_color: this.props.all_projects_data[this.props.selected_project].color,
              project_name: this.props.all_projects_data[this.props.selected_project].name,
              default_trade: template_job.default_trade,
              name: template_job.name,
              description: template_job.description,
              icon: template_job.icon,
              progress: 0,
              id: job_id,
              tasks: template_job.tasks || []
            };
            this.props.addJob(job_id, job, this.props.selected_project);
          }
        }
      }
    }
    else if (result.type==="LEAD") {
      const status_key = {
        "new_leads":"new",
        "scheduled_leads":"scheduled",
        "pending_leads":"pending",
        "estimate_leads":"estimate",
        "approved_leads":"approved",
        "rejected_leads":"rejected",
        "other_leads":""
      }

      let lead_groups = {
        new_leads: [],
        pending_leads: [],
        scheduled_leads: [],
        estimate_leads: [],
        approved_leads: [],
        rejected_leads: [],
        other_leads: []
      }
      for (let l in this.props.all_leads_data) {
        switch (this.props.all_leads_data[l].status) {
          case 'new':
            lead_groups.new_leads.push({...this.props.all_leads_data[l]});
            break;
          case 'pending':
            lead_groups.pending_leads.push({...this.props.all_leads_data[l]});
            break;
          case 'scheduled':
            lead_groups.scheduled_leads.push({...this.props.all_leads_data[l]});
            break;
          case 'estimate':
            lead_groups.estimate_leads.push({...this.props.all_leads_data[l]});
            break;
          case 'approved':
            lead_groups.approved_leads.push({...this.props.all_leads_data[l]});
            break;
          case 'rejected':
            lead_groups.rejected_leads.push({...this.props.all_leads_data[l]});
            break;
          default:
            lead_groups.other_leads.push({...this.props.all_leads_data[l]});
        }
      }

      if (source.droppableId === destination.droppableId) {
        const leads = [...lead_groups[destination.droppableId]];
        const items = reorder(
          leads,
          source.index,
          destination.index
        );

        this.props.updateLeadsData(items.reduce((obj, item, index) => {
          item.index = index
          obj[item.id] = item
          return obj
        }, {}));

      } else if (destination.droppableId === "project_leads") {

        let lead = {...this.props.all_leads_data[result.draggableId]};
        lead.status = status_key[destination.droppableId];
        lead[lead.status + "_date"] = lead[lead.status + "_date"] || new Date();

        this.props.editLead(lead.id, lead);


        let new_project = {};



        let id=GenerateCode();
        const project_color = GenerateRandomColor();
        let user_id = Math.random();
        let customer = {
          ...lead,
          id: user_id,
          trade_name: "Customer",
          jobs: [],
          tasks: []
        }
        let jobs = [];
        if (this.props.all_leads_data[lead.id].jobs) {
          let jobs_by_id = {};

          const lead_jobs = this.props.all_leads_data[lead.id].jobs;
          let template_job;

          for (let j=0; j<lead_jobs.length; j++) {
            template_job = lead_jobs[j];

            jobs_by_id[template_job] = {
              ...this.props.all_jobs_data[template_job],
              project_id: id,
              project_color: project_color,
              project_name: lead.first_name + " " + lead.last_name + " project"
            };
            jobs.push(template_job);
          }

          this.props.updateJobsData(jobs_by_id);
        }
        new_project = {
          name: lead.first_name + " " + lead.last_name + " project",
          address: lead.address,
          customer_id: user_id,
          color: project_color,
          id: id,
          jobs: jobs,
          tasks: [],
          photos: [],
          completed: false
        }

        this.props.updateUsersData({[user_id]: customer});

        this.props.updateProjectsData({[id]: new_project});

        // setTimeout(()=>{
          this.props.selectJob(id, null);
        // },1000);

        // this.props.openProjectEditor("NEWPROJECT", moved.removed);



      } else if (destination.droppableId === "archive_leads") {


        let lead = {...this.props.all_leads_data[result.draggableId]};
        lead.status = status_key[destination.droppableId];
        lead[lead.status + "_date"] = lead[lead.status + "_date"] || new Date();

        this.props.editLead(lead.id, lead);

      } else {

        let lead = {...this.props.all_leads_data[result.draggableId]};
        lead.status = status_key[destination.droppableId];
        lead[lead.status + "_date"] = lead[lead.status + "_date"] || new Date();

        this.props.editLead(lead.id, lead);
      }
    }
  };

  render() {


    return (
      <DragDropContext
        onBeforeDragStart={this.onBeforeDragStart}
        onDragStart={this.onDragStart}
        onDragUpdate={this.onDragUpdate}
        onDragEnd={this.onDragEnd()}
      >
        {this.props.children}
      </DragDropContext>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    all_projects_data: state.state.all_projects_data,
    all_leads_data: state.state.all_leads_data,
    all_jobs_data: state.state.all_jobs_data,
    all_project_templates_data: state.state.all_project_templates_data,
    all_job_templates_data: state.state.all_job_templates_data,
    selected_project: state.state.selected_project,
    selected_lead: state.state.selected_lead
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ editLead, editProject, addJob, updateJobsData, updateLeadsData, updateUsersData, updateProjectsData, selectJob}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(DragDropContainer);
