import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import LeadEditor from './lead-editor';
import JobEditor from './job-editor';

import EstimateProject from './estimate-project';

import {openLeadEditor, updateLeadsData, selectLead} from '../actions/actions';

class LeadEstimator extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // edit: this.props.edit || false
      selected_lead_data: this.props.all_leads_data[this.props.selected_lead] || {
        jobs:[]
      }
    };
  }
  componentDidUpdate(prevProps) {
    if (this.props.selected_lead) {
      if (this.props.selected_lead !== prevProps.selected_lead) {
        this.setState({
          selected_lead_data: this.props.all_leads_data[this.props.selected_lead] || {
            jobs:[]
          }
        });
      }
    }
  }

  handleCloseButton = (e) => {
    this.props.selectLead(null);
    setTimeout(()=>{
      this.setState({
        selected_lead_data: {
          jobs:[]
        }
      });
    },500);
  }

  handleChange = (e) => {
    this.setState({
      selected_lead_data: {...this.state.selected_lead_data, [e.target.name]:e.target.value}
    });
  }

  handleDescriptionChange = (value) => {
    this.setState({
      task_data: {...this.state.task_data, description:value}
    });
  }

  handleSaveChanges = (e) => {
    if (this.props.selected_lead === "NEWLEAD") {
      let id=Math.random();
      let new_lead = {
        ...this.state.selected_lead_data,
        id: id,
        new_date: new Date(),
        status: 'new'
      }
      this.props.updateLeadsData({
        [id]: new_lead
      });
    } else {
      this.props.updateLeadsData({
        [this.props.selected_lead]: {...this.state.selected_lead_data, jobs: this.props.all_leads_data[this.props.selected_lead].jobs}
      });
    }

    setTimeout(()=>{
      this.setState({
        selected_lead_data: {
          jobs:[]
        }
      });
    },500);
  }

  render() {
    return (
      <div className={"lead-estimator form" + (this.props.selected_lead && this.props.all_leads_data?" show":"")}>
        <div className="content">
          <header>
            <h1><i className="fas fa-phone"></i><span>{(this.state.selected_lead_data.first_name || "") + " " + (this.state.selected_lead_data.last_name || "")}</span></h1>
            <div>
              <button
                onClick={()=>{
                  this.props.openLeadEditor(this.props.selected_lead);
                }}
                className="icon-button"
              >
                <i className="fas fa-edit"></i>
              </button>
              <button
                onClick={()=>{
                  this.handleSaveChanges();
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-check"></i>
                <span>save</span>
              </button>
              <button
                onClick={()=>{
                  this.handleCloseButton();
                }}
                className=""
              >
                <i className="fas fa-times"></i>
                <span>cancel</span>
              </button>
            </div>
          </header>
          <main>
            <EstimateProject selected_lead_data={this.state.selected_lead_data} />
          </main>
          <LeadEditor />
          <JobEditor />
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    selected_lead: state.state.selected_lead,
    all_leads_data: state.state.all_leads_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({selectLead, updateLeadsData, openLeadEditor}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(LeadEstimator);
