import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import DatePicker from 'react-datepicker';

// import {USE_12_HOUR_CLOCK} from '../config/account-settings';
import "react-datepicker/dist/react-datepicker.css";


import {selectJobSchedule, updateJobsData, editJob, assignJobToUser} from '../actions/actions';


// const format = 'h:mm a';

class JobTimePicker extends React.Component {
  constructor(props) {
    super(props);
    var name="";
    var icon="";
    var assigned_user="";
    if (this.props.selected_job && this.props.all_jobs_data[this.props.selected_job].start_date) {
      this.default_start_time = this.props.all_jobs_data[this.props.selected_job].start_date;
      this.default_end_time = this.props.all_jobs_data[this.props.selected_job].end_date;
      name = this.props.all_jobs_data[this.props.selected_job].name;
      icon = this.props.all_jobs_data[this.props.selected_job].icon;
      assigned_user = this.props.all_jobs_data[this.props.selected_job].assigned_user || this.props.selected_job_schedule.user_id;
    }
    else {
      this.default_start_time = new Date();
      this.default_end_time = new Date();
    }
    this.state = {
      start_date: this.default_start_time,
      end_date: this.default_end_time,
      name: name,
      assigned_user: assigned_user,
      icon: icon
    };

  }

  componentDidMount () {
    var name="";
    var icon="";
    var assigned_user="";
    if (this.props.selected_job && this.props.all_jobs_data[this.props.selected_job].start_date) {
      this.default_start_time = this.props.all_jobs_data[this.props.selected_job].start_date;
      this.default_end_time = this.props.all_jobs_data[this.props.selected_job].end_date;
      name = this.props.all_jobs_data[this.props.selected_job].name;
      icon = this.props.all_jobs_data[this.props.selected_job].icon;
      assigned_user = this.props.all_jobs_data[this.props.selected_job].assigned_user || this.props.selected_job_schedule.user_id;
    }
    else {
      this.default_start_time = new Date();
      this.default_end_time = new Date();
    }
    this.setState({
      start_date: this.default_start_time,
      end_date: this.default_end_time,
      name: name,
      assigned_user: assigned_user,
      icon: icon
    });
  }
  componentDidUpdate(prevProps) {
    if (this.props.selected_job) {
      if ((this.props.selected_job !== prevProps.selected_job && this.props.all_jobs_data[this.props.selected_job]) || this.props.selected_job_schedule.user_id !== prevProps.selected_job_schedule.user_id) {
        if (this.props.all_jobs_data[this.props.selected_job].start_date) {
          this.default_start_time = this.props.all_jobs_data[this.props.selected_job].start_date;
          this.default_end_time = this.props.all_jobs_data[this.props.selected_job].end_date;

        }
        this.setState({
          start_date: this.default_start_time,
          end_date: this.default_end_time,
          name: this.props.all_jobs_data[this.props.selected_job].name,
          assigned_user: this.props.all_jobs_data[this.props.selected_job].assigned_user || this.props.selected_job_schedule.user_id,
          icon: this.props.all_jobs_data[this.props.selected_job].icon
        });
      }
    }
  }

  handleCloseButton = (e) => {
    this.props.selectJobSchedule(null, null);
  }



  handleChangeStartTime = (value) => {
    let start_dates = [...this.props.all_jobs_data[this.props.selected_job].start_dates];

    start_dates[this.props.selected_job_index] = value;

    this.props.editJob(this.props.selected_job, {start_dates:start_dates});
    this.props.assignJobToUser(this.props.all_jobs_data[this.props.selected_job].assigned_user, this.state.assigned_user, this.props.selected_job);
  }

  handleChangeEndTime = (value) => {
    let end_dates = [...this.props.all_jobs_data[this.props.selected_job].end_dates];

    end_dates[this.props.selected_job_index] = value;

    this.props.editJob(this.props.selected_job, {end_dates:end_dates});
    this.props.assignJobToUser(this.props.all_jobs_data[this.props.selected_job].assigned_user, this.state.assigned_user, this.props.selected_job);
  }

  // handleChangeStart = date => {
  //   if (!this.state.endDate || date < this.state.endDate) {
  //     this.setState({
  //       startDate: date,
  //       pickend: true
  //     });
  //   }
  // };
  // handleChangeEnd = date => {
  //   if (!this.state.startDate || date > this.state.startDate) {
  //     this.setState({
  //       endDate: date,
  //       pickend: false
  //     });
  //   }
  // };

  handleSaveChanges = (e) => {
    if (this.props.selected_job === "NEWLEAD") {
      let id=Math.random();
      let new_job = {
        ...this.state.selected_job_data,
        id: id,
        new_date: new Date(),
        status: 'new'
      }
      this.props.updateLeadsData({
        [id]: new_job
      });
    } else {
      this.props.updateLeadsData({
        [this.props.selected_job]: this.state.selected_job_data
      });
    }
  }

  render() {

    // const user = this.props.all_users_data[this.props.selected_job_schedule.user_id] || {};
    let user = {};
    if (this.props.all_users_data) {
      user = this.props.all_users_data[this.state.assigned_user] || {};
    }
    return (
      <div className={"job-time-picker form" + (this.props.selected_job && this.props.all_jobs_data && this.props.selected_job_schedule && this.props.selected_job_schedule.user_id?" show":"")}>
      {(this.props.selected_job && this.props.all_jobs_data) &&
        (<div className="content">
          <header>
            <h1><i className={this.state.icon}></i><span>{this.state.name}</span></h1>
            <div>
              <button
                onClick={()=>{
                  this.handleCloseButton();
                }}
                className="icon-button"
              >
                <i className="fas fa-check"></i>
              </button>
            </div>
          </header>
          <main>
            <p>
              <span>assigned to: </span>
              <span>{user.first_name + " " + user.last_name + " "}</span>
            </p>
            <div>
              <div>
                <label>from</label>
                <DatePicker
                  selected={new Date(this.state.start_date)}
                  className=""
                  inline
                  onChange={this.handleChangeStartTime}
                  showTimeSelect
                  showTimeSelectOnly
                  timeIntervals={15}
                  minTime={new Date(new Date(this.state.start_date).setHours(0, 0))}
                  maxTime={new Date(this.state.end_date)}
                  dateFormat="h:mm aa"
                  timeCaption="start time"
                />
              </div>
              <div>
                <label>to</label>
                <DatePicker
                  selected={new Date(this.state.end_date)}
                  className=""
                  inline
                  onChange={this.handleChangeEndTime}
                  showTimeSelect
                  showTimeSelectOnly
                  timeIntervals={15}
                  minTime={new Date(this.state.start_date)}
                  maxTime={new Date(new Date(this.state.end_date).setHours(23, 55))}
                  dateFormat="h:mm aa"
                  timeCaption="end time"
                />
              </div>
            </div>
          </main>
        </div>)
      }
      </div>
    );
  }
}


