const mapStateToProps = (state) => {
  return {
    selected_job_schedule: state.state.selected_job_schedule,
    selected_job: state.state.selected_job,
    selected_job_index: state.state.selected_job_index,
    all_jobs_data: state.state.all_jobs_data,
    all_users_data: state.state.all_users_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({selectJobSchedule, updateJobsData, editJob, assignJobToUser}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(JobTimePicker);
