import React from 'react';
import {
  Route
} from 'react-router-dom'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {updateProjectsData, selectJob, selectJobSchedule} from '../actions/actions'

import ProjectList from './project-list';
import ProjectOpener from './project-opener';
import ProjectEditor from './project-editor';
import InvoiceEditor from './invoice-editor';
import ProjectNotes from './project-notes';
import ProjectPhotoGallery from './project-photo-gallery';

import {URLString} from '../utilities/string';
import {ProjectData} from '../utilities/data';

class PageProjects extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loaded: false
    };
  }

  componentDidMount() {
    var projects_by_id = {};
    const keys = Object.keys(this.props.all_projects_data);
    const location_array = this.props.location.pathname.split("/");
    // let selected_id;

    if (keys.length) {
      for (var pr in this.props.all_projects_data) {
        if (URLString(this.props.all_projects_data[pr].id) === location_array[2]) {
          // selected_id = this.props.all_projects_data[pr].id;
        }
      }
      projects_by_id = {...this.props.all_projects_data};
      setTimeout(()=>{
        this.setState({
          loaded: true,
        });
      }, 1);
    }
    else {

      this.temp_load_data = setTimeout(()=>{

        for (var p=0;p<ProjectData.length; p++) {
          projects_by_id[ProjectData[p].id]=ProjectData[p];
          if (URLString(ProjectData[p].id) === location_array[2]) {
            // selected_id = ProjectData[p].id;
          }
        }
        this.props.updateProjectsData(projects_by_id);

        setTimeout(()=>{
          this.setState({
            loaded: true,
          });
        }, 1);
      }, 2000);
    }

    this.props.selectJobSchedule(null);
  }

  componentWillUnmount () {
    window.clearTimeout(this.temp_load_data);
  }

  render() {
    return (
      <section id="page-projects" className={"projects " + (this.state.loaded?"loaded":"loading")}>
        <div className="load-background"></div>
        <Route path="/projects/:p" component={ProjectOpener} />
        <Route path="/projects" component={ProjectList} />
        <ProjectEditor />
        <ProjectNotes />
        <ProjectPhotoGallery />
        <InvoiceEditor />
      </section>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    selected_project: state.state.selected_project,
    all_projects_data: state.state.all_projects_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({updateProjectsData, selectJob, selectJobSchedule}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(PageProjects);
