import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import Gallery from 'react-photo-gallery';

import SelectedImage from './selected-image';
import Editor from './editor';

import {openPhotoGallery, updatePhotosData, editPhoto} from '../actions/actions';

import {PhotosById} from '../utilities/data';


// const format = 'h:mm a';

class ProjectPhotoGallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      project_data: {},
      photos: [],
      columns: 6
    }
  }

  callAPI () {
    this.temp_api_timeout = setTimeout(()=>{
      let photos = [];
      let photos_by_id = {};
      let photos_to_update = {};
      if (!this.props.all_photos_data[this.props.all_projects_data[this.props.open_photos].photos[0]]) {
        photos_by_id = {...PhotosById};
      } else {
        photos_by_id = {...this.props.all_photos_data}
      }

      for (var p=0; p<this.props.all_projects_data[this.props.open_photos].photos.length; p++) {
        var photo_id =  this.props.all_projects_data[this.props.open_photos].photos[p];
        photos.push(photos_by_id[photo_id]);
        photos_to_update[photo_id] = photos_by_id[photo_id];
      }

      this.props.updatePhotosData(photos_to_update);

      this.setState({
        photos: photos
      });
    }, 500);

  }

  componentDidUpdate(prevProps) {
    if (this.props.open_photos) {
      if (this.props.open_photos !== prevProps.open_photos && this.props.all_projects_data[this.props.open_photos]) {
        let photos = this.props.all_projects_data[this.props.open_photos].photos;
        if (photos.length) {
          this.callAPI();
        }
        this.setState({
          project_data: this.props.all_projects_data[this.props.open_photos],
          photos_ids: this.props.all_projects_data[this.props.open_photos].photos
        });
      }
    }
  }

  handleCloseButton = (e) => {
    this.props.openPhotoGallery(null);
  }

  handleZoomOutButton = (e) => {
    if (this.state.columns < 15 && this.state.photos && this.state.columns < this.state.photos.length-1) {
      this.setState({
        columns: this.state.columns+1
      })
    }
  }

  handleZoomInButton = (e) => {
    if (this.state.columns > 1) {
      this.setState({
        columns: this.state.columns-1
      })
    }
  }

  selectPhoto = (_e, obj) => {
    let photos = this.state.photos;
    // photos[obj.index].selected = !photos[obj.index].selected;
    let selected = photos[obj.index];
    this.setState({
      photos: photos,
      selected: this.props.all_photos_data[selected.id],
      show_selected: !this.state.selected || this.state.selected.id !== selected.id || !this.state.show_selected ? this.props.all_photos_data[selected.id] : null
    });
  }
  toggleSelect = () => {
    let photos = this.state.photos.map((photo, index) => {
      return { ...photo, selected: !this.state.selectAll };
    });
    this.setState({ photos: photos, selectAll: !this.state.selectAll });
  }


  handleChange = (e) => {
    this.setState({
      selected: {...this.state.selected, [e.target.name]:e.target.value}
    });

    this.props.editPhoto(this.state.selected.id, {...this.state.selected, [e.target.name]:e.target.value});
  }

  handleDescriptionChange = (value) => {
    this.props.editPhoto(this.state.selected.id, {...this.state.selected, description:value});
  }

  render() {
    return (
      <div className={"photo-gallery form" + (this.props.open_photos && this.props.all_projects_data ? " show":"")}>
      {(this.state.photos) &&
        (<div className="content">
          <header>
            <h1>{this.state.project_data.name}</h1>
            <div>
              <button
                onClick={()=>{
                  this.toggleSelect();
                }}
                className="icon-button"
              >
                <i className="fas fa-check-double"></i>
              </button>
              <button
                onClick={()=>{
                  this.handleZoomOutButton();
                }}
                className="icon-button"
              >
                <i className="fas fa-search-minus"></i>
              </button>
              <button
                onClick={()=>{
                  this.handleZoomInButton();
                }}
                className="icon-button"
              >
                <i className="fas fa-search-plus"></i>
              </button>
              <button
                onClick={()=>{
                  this.handleCloseButton();
                }}
                className="icon-button"
              >
                <i className="fas fa-times"></i>
              </button>
            </div>
          </header>
          <main>
            <div className="gallery-container">
              <Gallery
                photos={this.state.photos}
                columns={this.state.columns}
                onClick={this.selectPhoto}
                ImageComponent={SelectedImage}
              />
            </div>
              <div className={"selected-panel" + (this.state.show_selected ? " show" : "")}>
                {this.state.selected && (
                  <div>
                    <div className="selected-photo">
                      <img
                        alt={this.state.selected.name}
                        {...this.state.selected}
                      />
                    </div>
                    <div className="photo-details">
                      <label>caption</label>
                      <input name="name" type="text" placeholder="photo caption" value={this.state.selected.name || ""} onChange={this.handleChange} />
                      <label>notes</label>
                      <Editor
                        defaultValue={(this.state.selected.description) || ""}
                        onChange={this.handleDescriptionChange}
                        refresh={this.state.selected.id}
                      />
                    </div>
                  </div>
                )}
              </div>
          </main>
        </div>)
      }
      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    open_photos: state.state.open_photos,
    all_projects_data: state.state.all_projects_data,
    all_photos_data: state.state.all_photos_data,
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({openPhotoGallery, updatePhotosData, editPhoto}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(ProjectPhotoGallery);
