import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {selectTask, addJob, editJob, addTask, closeJobCreator, updateJobTemplatesData, updateProjectTemplatesData} from '../actions/actions';

import {ProjectTemplates, JobTemplates} from '../utilities/data'

import {GenerateCode} from '../utilities/data';

import {Droppable, Draggable } from 'react-beautiful-dnd';

const icons = [
  "fas fa-toolbox",
  "fas fa-paint-roller",
  "fas fa-wrench",
  "fas fa-screwdriver",
  "fas fa-hammer",
  "fas fa-tint",
  "fas fa-bolt",
  "fas fa-clipboard-list",
  "fas fa-tape",
  "fas fa-file-signature",
  "fas fa-dollar-sign"
];




const getProjectItemStyle = (item, isDragging, draggableStyle) => {
  return {
    // some basic styles to make the items look a bit nicer
    color: "black",
    background: "#c1c1c1",
    padding: "1rem",
    margin: "0 .5rem .5rem 0",
    borderRadius: "5px",
    width: "auto",
    // change background colour if dragging
    // background: heat_map[heat],//isDragging ? 'lightgreen' : 'grey',
    // boxShadow: "rgba(0, 0, 0, 0.12) 0 0 1rem",
    // styles we need to apply on draggables
    ...draggableStyle
  }
};

const getItemStyle = (item, isDragging, draggableStyle) => {
  return {
    // some basic styles to make the items look a bit nicer
    color: "black",
    background: "#c1c1c1",
    padding: "1rem",
    margin: "0 .5rem .5rem 0",
    borderRadius: "5px",
    width: "auto",
    // change background colour if dragging
    // background: heat_map[heat],//isDragging ? 'lightgreen' : 'grey',
    // boxShadow: "rgba(0, 0, 0, 0.12) 0 0 1rem",
    // styles we need to apply on draggables
    ...draggableStyle
  }
};


const getListStyle = isDraggingOver => ({
    background: isDraggingOver ? '#e4e4e4' : 'transparent',
    display: "flex",
    padding: ".5rem 0",
    width: "100%",
    minHeight: "5rem",
    flex: "1 1 auto",
    position: "relative",
    zIndex: "1",
    flexFlow: "row wrap"
});


const getDropListStyle = isDraggingOver => ({
    background: isDraggingOver ? 'rgba(250, 250, 250, 0.20)' : 'transparent',
    width: "100%",
    height: "8rem",
    bottom: "100%",
    left: 0,
    position: "absolute",
    zIndex: "-1"
});


class JobCreator extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // edit: this.props.edit || false\
      icon: icons[0],
      jobOrTask: "job",
      job_data: {},
      tab: "project-templates"
    };
  }

  componentDidMount () {
    if (!this.props.all_job_templates_data) {
      this.props.updateProjectTemplatesData(ProjectTemplates);
      this.props.updateJobTemplatesData(JobTemplates);
    }
  }
  componentDidUpdate(prevProps) {
    if (this.props.open_job_creator) {
      if (!prevProps.open_job_creator || this.props.open_job_creator.job_id !== prevProps.open_job_creator.job_id) {
        this.setState({
          job_data: this.props.all_jobs_data[this.props.open_job_creator.job_id] || {},
          icon: this.props.open_job_creator.job_id ? this.props.all_jobs_data[this.props.open_job_creator.job_id].icon : icons[0]
        });
      }
    }
  }

  handleCloseButton = (e) => {
    this.props.closeJobCreator();
    setTimeout(()=>{
      this.setState({
        icon: icons[0],
        jobOrTask: "job",
        job_data: {}
      });
    },500);
  }

  handleChange = (e) => {
    this.setState({
      job_data: {...this.state.job_data, [e.target.name]:e.target.value}
    });
  }

  validate = () => {
    let valid = true;
    if (!this.state.job_data.name) {
      valid = false;
    }
    return valid;
  }

  addJob = (item) => (
    (_e) => {
      const id = GenerateCode();
      this.props.addJob(id, {
        ...this.state.job_data,
        id: id,
        tasks:[],
        start_dates:[],
        end_dates:[],
        icon: this.state.icon,
        project_id: this.props.open_job_creator.project_id,
        project_name: this.props.all_projects_data[this.props.open_job_creator.project_id].name,
        project_color: this.props.all_projects_data[this.props.open_job_creator.project_id].color
      }, this.props.open_job_creator.project_id);
    }
  )

  handleTypeChange = (changeEvent) => {
    this.setState({
      jobOrTask: changeEvent.target.value
    });
  };

  handleIconChange = (changeEvent) => {
    this.setState({
      icon: changeEvent.target.value
    });
  };


  createJob = (item, index) => {
    return (<Draggable
      key={item.id}
      draggableId={item.id}
      index={index}>
      {(provided, snapshot) => (
        <div
          className="job"
          ref={provided.innerRef}
          {...provided.draggableProps}
          {...provided.dragHandleProps}

          style={getItemStyle(
            item,
            snapshot.isDragging,
            provided.draggableProps.style
          )}>

          <i className={item.icon}></i>
          <span>{item.name}</span>
        </div>
      )}
    </Draggable>);
  }

  createProject = (item, index) => {
    return (<div className="drag-project" key={item.id}>
      <Draggable
        key={item.id}
        draggableId={item.id}
        index={index}
      >
        {(provided, snapshot) => (
          <div
            ref={provided.innerRef}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
            style={getProjectItemStyle(
              item,
              snapshot.isDragging,
              provided.draggableProps.style
            )}>
            <div>{item.name}</div>
          </div>
        )}
      </Draggable>
      {this.props.all_job_templates_data && (
        <div className="jobs">{
          item.jobs.map((job, index) => (
            <div key={job}>
              <i className={this.props.all_job_templates_data[job].icon}></i>
              {this.props.all_job_templates_data[job].name}
            </div>
          ))
        }</div>
      )}
    </div>);
  }


  changeMode (mode) {
    this.setState({
      tab: mode
    });
  }

  render() {

    let icon_radios = [];

    for (let i=0; i<icons.length; i++) {
      icon_radios.push ((
        <div key={"icon " + i} className="radio">
          <label>
            <input
              type="radio"
              value={icons[i]}
              checked={this.state.icon === icons[i]}
              onChange={this.handleIconChange}
            />
            <i className={icons[i]}></i>
          </label>
        </div>
      ));
    }

    let job_templates = [];
    let project_templates = [];
    let sort_value = "name";

    if (this.props.all_job_templates_data) {
      for (var j in this.props.all_job_templates_data) {
        job_templates.push(this.props.all_job_templates_data[j]);
      }

      job_templates.sort(function(a, b){
        var x = a[sort_value].toLowerCase();
        var y = b[sort_value].toLowerCase();
        if (x < y) {return -1;}
        if (x > y) {return 1;}
        return 0;
      });
    }

    if (this.props.all_project_templates_data) {
      for (var p in this.props.all_project_templates_data) {
        project_templates.push(this.props.all_project_templates_data[p]);
      }

      project_templates.sort(function(a, b){
        var x = a[sort_value].toLowerCase();
        var y = b[sort_value].toLowerCase();
        if (x < y) {return -1;}
        if (x > y) {return 1;}
        return 0;
      });
    }


    return (
      <div className={"job-creator form" + (this.props.open_job_creator && this.props.open_job_creator && this.props.all_tasks_data?" show":"")}>
        <div className="content">
          <header>
            <h1><span>{("Add Jobs")}</span></h1>
            <div>
              <button
                onClick={()=>{
                  this.handleCloseButton();
                }}
                className="icon-button"
              >
                <i className="fas fa-times"></i>
              </button>
            </div>
          </header>
          <main>
            <div>
            {(project_templates.length && job_templates.length) && (
              <div className="job-templates">

                <Droppable
                  className="droppable-column"
                  droppableId={"project-timeline-" + this.props.selected_project}
                  type="JOB"
                  direction="horizontal"
                >
                  {(provided, snapshot) => (
                    <div
                      ref={provided.innerRef}
                      style={getDropListStyle(snapshot.isDraggingOver)}
                    >

                    {provided.placeholder}

                    </div>
                  )}
                </Droppable>
                <header>
                  <button className={"tab-button " + (this.state.tab === "project-templates"?"selected":"")} onClick={()=>{this.changeMode("project-templates")}}>project templates</button>
                  <button className={"tab-button " + (this.state.tab === "job-templates"?"selected":"")} onClick={()=>{this.changeMode("job-templates")}}>job templates</button>
                </header>
                <div className="tab-panel">
                  <div className={this.state.tab === "project-templates"?"show":""}>
                    {this.state.tab === "project-templates" && (
                      <Droppable
                        className="droppable-column"
                        droppableId="template_projects_project"
                        type="JOB"
                        isDropDisabled
                      >
                        {(provided, snapshot) => (
                          <div
                            ref={provided.innerRef}
                            style={getListStyle(snapshot.isDraggingOver)}>
                            {
                              project_templates.map((item, index) => (
                                this.createProject(item, index)
                              ))
                            }
                          </div>
                        )}
                      </Droppable>
                    )}
                  </div>
                  <div className={this.state.tab === "job-templates"?"show":""}>
                    {this.state.tab === "job-templates" && (
                      <Droppable
                        className="droppable-column"
                        droppableId="template_jobs"
                        type="JOB"
                        direction="horizontal"
                        isDropDisabled
                      >
                        {(provided, snapshot) => (
                          <div
                            ref={provided.innerRef}
                            style={getListStyle(snapshot.isDraggingOver)}>
                            {
                              job_templates.map((item, index) => (
                                this.createJob(item, index)
                              ))
                            }
                          </div>
                        )}
                      </Droppable>
                    )}
                  </div>
                </div>
              </div>
            )}
            </div>
          </main>
        </div>
      </div>
    );
  }
}

