const mapStateToProps = (state) => {
  return {
    selected_task: state.state.selected_task,
    open_job_creator: state.state.open_job_creator,
    all_jobs_data: state.state.all_jobs_data,
    all_tasks_data: state.state.all_tasks_data,
    all_projects_data: state.state.all_projects_data,
    all_job_templates_data: state.state.all_job_templates_data,
    all_project_templates_data: state.state.all_project_templates_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({selectTask, addJob, editJob, addTask, closeJobCreator, updateJobTemplatesData, updateProjectTemplatesData}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(JobCreator);
