import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {UsersById, TasksById} from '../utilities/data'

import {openProjectSummary, updateUsersData, updateTasksData} from '../actions/actions';

class ProjectNotes extends React.Component {
  state = {
    project: null
  }

  callAPI () {
    let updateUsers = {};
    if (!this.props.all_users_data[this.props.all_projects_data[this.props.open_project_summary].customer_id]) {
      updateUsers[this.props.all_projects_data[this.props.open_project_summary].customer_id] = {...UsersById[this.props.all_projects_data[this.props.open_project_summary].customer_id]};
    }

    let updateTasks = {};

    for (var t=0; t<this.props.all_projects_data[this.props.open_project_summary].tasks.length; t++) {
      let task_id = this.props.all_projects_data[this.props.open_project_summary].tasks[t];
      if (!this.props.all_tasks_data[task_id]) {
        updateTasks[task_id] = {...TasksById[task_id]};
        if (TasksById[task_id].assigned_user && !this.props.all_users_data[TasksById[task_id].assigned_user]) {
          updateUsers[TasksById[task_id].assigned_user] = {...UsersById[TasksById[task_id].assigned_user]};
        }
      }
    }
    for (var j=0; j<this.props.all_projects_data[this.props.open_project_summary].jobs.length; j++) {
      let job = this.props.all_jobs_data[this.props.all_projects_data[this.props.open_project_summary].jobs[j]];

      if (this.props.all_jobs_data[job.id].assigned_user && !this.props.all_users_data[this.props.all_jobs_data[job.id].assigned_user]) {
        updateUsers[this.props.all_jobs_data[job.id].assigned_user] = {...UsersById[this.props.all_jobs_data[job.id].assigned_user]};
      }


      for (t=0; t<job.tasks.length; t++) {
        let task_id = job.tasks[t];
        if (!this.props.all_tasks_data[task_id]) {
          updateTasks[task_id] = {...TasksById[task_id]};

          if (TasksById[task_id].assigned_user && !this.props.all_users_data[TasksById[task_id].assigned_user]) {
            updateUsers[TasksById[task_id].assigned_user] = {...UsersById[TasksById[task_id].assigned_user]};
          }
        }
      }
    }
    this.props.updateUsersData(updateUsers);
    this.props.updateTasksData(updateTasks);
  }

  componentDidUpdate(prevProps) {
    if (this.props.open_project_summary && this.props.all_projects_data) {
      if (this.props.open_project_summary !== prevProps.open_project_summary) {
        this.callAPI();
        this.setState({
          project: this.props.all_projects_data[this.props.open_project_summary]
        });
      }
    }
  }


  handleCloseButton = (e) => {
    this.props.openProjectSummary(null);
  }

  handlePrintButton = (e) => {
    window.printDiv("print-project-notes");
  }


  render() {
    if (this.props.all_projects_data && this.state.project) {
      let project = this.state.project;
      let customer = project.customer_id ? this.props.all_users_data[project.customer_id] || {} : {}
      let note_objects = [];
      let note_nodes = [];

      if (project.jobs && project.tasks) {

        for (var j = 0; j<project.jobs.length; j++) {
          let job = this.props.all_jobs_data[project.jobs[j]];
          // if (job.description || job.tasks.length) {
          let job_object = {
            icon: job.icon,
            name: job.name,
            assigned: (job.assigned_user ? this.props.all_users_data[job.assigned_user] : ""),
            start_date: job.start_dates[0],
            end_date: job.end_dates[0],
            description: job.description
          };

          if (job.tasks.length) {
            job_object.tasks = [];
            for (var t=0; t<job.tasks.length; t++) {
              let task = this.props.all_tasks_data[job.tasks[t]];
              let task_user = (task.assigned_user ? this.props.all_users_data[task.assigned_user] : "");
              job_object.tasks.push((
                <li key={j+""+t+"task"}>
                  <h5>{task.name}<span className="user">{task_user && (task_user.first_name + " " + task_user.last_name)}</span></h5>
                  { task.description && (
                    <p key={j+""+t+"descripiton"} dangerouslySetInnerHTML={{__html: task.description}}/>
                  )}
                </li>
              ));
            }
          }

          // }

          note_objects.push(job_object);
        }
      }

      note_objects.sort(function(a, b){
        var x = -a.start_date || -b.start_date-10;
        var y = -b.start_date || -a.start_date-10;
        if (x < y) {return 1;}
        if (x > y) {return -1;}
        return 0;
      });

      for (var n=0; n<note_objects.length; n++) {
        var note = note_objects[n];

        let date=[];
        if (note.start_date) {
          date = (
            <span className="date" key={"dates"+n}>
              <span className="date-value">
                {note.start_date.toLocaleDateString()}
              </span>
              <span className="time-value">
                {note.start_date.toLocaleTimeString() + (note.end_date ? " - " + note.end_date.toLocaleTimeString() : "")}
              </span>
            </span>
          );
        } else if (note.deadline) {
          date = (
            <span className="date" key={"deadline"+n}>{"due by " + note.deadline.toLocaleDateString()}</span>
          );
        } else {
          date = (
            <span className="date tbd" key={"dates"+n}>{"to be scheduled"}</span>
          );
        }

        let description;
        if (note.description) {
          description = (
            <p key={"description"+n} dangerouslySetInnerHTML={{__html: note.description}} />
          );
        }

        let assigned = (
          <span className="user" key={"assigned"+n}>[
            {note.assigned ?
              (note.assigned.first_name + " " + note.assigned.last_name):
              (<span className="tbd">to be assigned</span>)
            }
          ]</span>
        );
        // }

        let name;
        if (note.name) {
          name = (
            <h3 key={"name"+n}>
              <span>
                {note.icon && (<i className={note.icon} />)}
                {note.name}
                {assigned}
              </span>
              {date}
            </h3>
          );
          note_nodes.push((
            <li key={"job"+n}>
              {name}
              {description}
              {note.tasks && (
                <div>
                  <h4 key={"tasklabel"+n}>job tasks</h4>
                  <ul key={"tasks"+n}>
                    {note.tasks}
                  </ul>
                </div>
              )}
            </li>
          ));
        }

      }


      const description = window.unescape(project.description);

      return (
        <div className={"project-notes form" + (this.props.open_project_summary && this.props.all_projects_data?" show":"")}>
          <div className="content">
            <header>
              <div>
                <button
                  onClick={()=>{
                    this.handlePrintButton();
                  }}
                  className=""
                >
                  <i className="fas fa-print"></i>
                  <span>print</span>
                </button>
                <button
                  onClick={()=>{
                    this.handleCloseButton();
                  }}
                  className=""
                >
                  <i className="fas fa-times"></i>
                  <span>close</span>
                </button>
              </div>
            </header>
            <main>
              <div id="print-project-notes" className="document">
                <h1>
                  <span>{(project.name || "")}</span>
                </h1>
                <h2>customer</h2>
                <div>
                  <div>
                    <h3>name</h3>
                    <p>{(customer.last_name ? customer.first_name + " " + customer.last_name : "")}</p>
                  </div>
                  <div>
                    <h3>address</h3>
                    <p>{project.address}</p>
                  </div>
                </div>
                {project.description ? [
                  (<h2 key="pnotes">project notes</h2>),
                  (<p key="pdescription" dangerouslySetInnerHTML={{__html: description}} />)
                ]:[]}
                <h2>project timeline</h2>
                <ul>
                  {note_nodes}
                </ul>
              </div>
            </main>
          </div>
        </div>
      );
    }
    else {
      return [];
    }
  }
}


const mapStateToProps = (state) => {
  return {
    open_project_summary: state.state.open_project_summary,
    all_jobs_data: state.state.all_jobs_data,
    all_tasks_data: state.state.all_tasks_data,
    all_users_data: state.state.all_users_data,
    all_projects_data: state.state.all_projects_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({openProjectSummary, updateUsersData, updateTasksData}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(ProjectNotes);
