import React from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {editJob, deleteJob, selectJob, openJobCreator} from '../actions/actions'

import Editor from './editor'
import Scheduler from './scheduler'
import TaskList from './task-list'
import TaskEditor from './task-editor'
import JobCreator from './job-creator'
import JobTimePicker from './job-time-picker'

// import ProjectSummary from './project-summary';

import {JobsById} from '../utilities/data'
// import {JobsById, TasksById} from '../utilities/data'

class JobDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loaded: false,
      show_content: true,
      detail_mode: "schedule",
      job_data: null
    }
  }

  callAPI (detail) {
    if (this.props.selected_job) {
      this.temp_api_call = setTimeout(()=>{
        this.setState({
          loaded: true
        });
        const job_data = this.props.all_jobs_data[this.props.selected_job] || JobsById[this.props.selected_job];
        if (detail) {
          this.setState({
            detail_mode: job_data.start_dates[0] ? "task" : "schedule"
          });
        }
        if (this.state.show_content){
          this.setState({
            start_description: job_data.description,
            job_data: job_data
          });
        } else {
          this.setState({
            start_description: job_data.description,
            temp_job_data: job_data
          });
        }
      }, 30);
    }
  }

  componentDidMount () {
    this.callAPI(true);
  }

  componentDidUpdate (prevProps) {
    if (this.props.selected_job !== prevProps.selected_job || (!this.props.open_job_editor && prevProps.open_job_editor && this.props.selected_job === prevProps.open_job_editor.job_id)) {
      // this.setState({loaded: false});
      //API CALL HERE
      this.callAPI();

      // if (this) {
      //   this.setState({loaded: false, show_content: false});
      //   this.show_content_animation = setTimeout(()=>{
      //     this.setState({
      //       show_content: true
      //     });
      //     if (this.state.loaded) {
      //       this.setState({
      //
      //         job_data: this.state.temp_job_data,
      //         temp_job_data: null
      //       });
      //     }
      //   }, 500);
      // }
    }
  }

  componentWillUnmount () {
    window.clearTimeout(this.temp_api_call);
    window.clearTimeout(this.show_content_animation);
  }

  changeMode (mode) {
    this.setState({
      detail_mode: mode,
      start_description: this.state.job_data.description
    });
  }

  handleDescriptionChange = (value) => {
    this.setState({
      job_data: {...this.state.job_data, description: value}
    });
    this.props.editJob(this.props.selected_job, {...this.state.job_data, description: value});
  }

  handleCheck (mode) {
    this.setState({
      job_data: {...this.state.job_data, progress: 100}
    });
    this.props.editJob(this.props.selected_job, {...this.state.job_data, progress: 100});
  }

  handleEditButton = (_e) => {
    this.props.openJobCreator(this.props.selected_project, this.props.selected_job);
  }

  handleTrashButton = (_e) => {
    //TODO add delete

    this.props.deleteJob(this.props.selected_job, this.props.selected_project);
  }

  updateJobProgress = (job_id, tasks, progress) => {
    // JobsById[job_id].progress = progress;
    // let task_ids = [];
    // let task;
    // for (let t=0; t<tasks.length; t++) {
    //   task = tasks[t];
    //   task_ids.push(task.id);
    //   TasksById[task.id] = {...task};
    // }
    // JobsById[job_id].tasks = task_ids;
    // this.props.updateJob(job_id, {...JobsById[job_id]});
  }


  handleCloseButton = (e) => {
    this.props.selectJob(this.props.selected_project);
  }

  render () {
    return (
      <div className={"job-detail-panel" +
        (this.state.loaded&&this.state.show_content?" loaded":"")
      }>
        { this.props.selected_job ? (
          this.state.job_data && (
            <div>
              <div className="job-details">
                <header>
                  <div>
                    <h2>{this.state.job_data.icon?(<i className={this.state.job_data.icon} />):[]}{this.state.job_data.name}</h2>
                    <button className={"tab-button " + (this.state.detail_mode === "schedule"?"selected":"")} onClick={()=>{this.changeMode("schedule")}}>schedule job</button>
                    <button className={"tab-button " + (this.state.detail_mode === "task"?"selected":"")} onClick={()=>{this.changeMode("task")}}>manage tasks</button>
                    <button className={"tab-button " + (this.state.detail_mode === "note"?"selected":"")} onClick={()=>{this.changeMode("note")}}>take notes</button>
                    <button disabled={!this.props.all_jobs_data[this.props.selected_job].start_dates[0]} className={"icon-button check-button" + (this.props.all_jobs_data[this.props.selected_job].progress === 100 ? " completed":"")} onClick={()=>{this.handleCheck()}}><i className="fas fa-check-circle check"></i><i className="far fa-circle"></i></button>
                  </div>
                  <div>
                    <button title="remove" className={"icon-button "} onClick={this.handleTrashButton}><i className="fas fa-trash" /></button>
                    <button title="edit" className={"icon-button "} onClick={this.handleEditButton}><i className="fas fa-edit" /></button>
                    <button title="close" onClick={this.handleCloseButton} className="icon-button"><i className="fas fa-times"></i></button>
                  </div>
                </header>
                <div className="tab-panel">
                  <div className={this.state.detail_mode === "note"?"show":""}>
                    {
                      this.state.detail_mode === "note" &&
                      (<Editor
                        defaultValue={this.state.start_description || ""}
                        onChange={this.handleDescriptionChange}
                        refresh={this.state.job_data.id}
                      />)
                    }
                  </div>
                  <div className={this.state.detail_mode === "task"?"show":""}>
                    {
                      this.state.detail_mode === "task"?
                        (<TaskList
                          selected_job={this.state.job_data.id}
                          default_trade={this.state.job_data.default_trade}
                         />)
                      :
                        []
                    }
                  </div>
                  <div className={this.state.detail_mode === "schedule"?"show":""}>
                    {
                      this.state.detail_mode === "schedule"&&
                      (<Scheduler
                        selected_job={this.state.job_data.id}
                        selected_job_data={{...this.state.job_data, index: this.props.selected_job_index, start_date: this.state.job_data.start_dates[this.props.selected_job_index], end_date: this.state.job_data.end_dates[this.props.selected_job_index]}}
                        default_trade={this.state.job_data.default_trade}
                       />)
                    }
                  </div>
                </div>
              </div>

              <span className="no-task-selected load-screen">
                <h3>loading...</h3>
              </span>
            </div>
          )
        ):(
          <div className="no-task-selected">
            <i className="fas fa-hand-point-up"></i>
            <h3>select a job</h3>
          </div>
        )}
        <JobCreator />
        <TaskEditor />
        <JobTimePicker />
      </div>
    );
  }
}

// <ProjectSummary />

const mapStateToProps = (state) => {
  return {
    selected_project: state.state.selected_project,
    selected_job: state.state.selected_job,
    selected_job_index: state.state.selected_job_index,
    open_job_editor: state.state.open_job_editor,
    all_jobs_data: state.state.all_jobs_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({editJob, deleteJob, selectJob, openJobCreator}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(JobDetails);
